<template>
    <div>
        <v-img
                src="../assets/LOGO-USACH_COLOR.png"
                width="20%"
                height="10%">
        </v-img>
        <v-row
                align="center"
                justify="center"
                style="width: 100%; position:fixed; top:25%"

        >

            <v-col
                    cols="12"
                    sm="8"
                    md="4"
            >
                <v-alert type="error" dismissible v-if="issueLogIn === 1 || issueLogIn === 2" style="width: 80%; margin-left:10%">
                    {{this.messageError}}
                </v-alert>
                <v-alert type="error" dismissible v-if="issueLogIn === 3" style="width: 80%; margin-left:10%">
                    {{this.messageBan}}
                </v-alert>

                <v-card class="elevation-12">
                    <v-toolbar
                            color="primary"
                            dark
                            flat
                    >
                        <v-toolbar-title>Ingrese sus credenciales</v-toolbar-title>
                        <v-spacer />
                    </v-toolbar>
                    <v-card-text>
                        <v-form>
                            <v-text-field
                                    label="Rut"
                                    name="login"
                                    type="text"
                                    v-model="username"
                                    v-mask="['#.###.###-X','##.###.###-X']"
                                    hint="Ejemplo: 11.111.111-1 o 1.111.111-1" 

                            />

                            <v-text-field
                                    id="password"
                                    label="Contraseña"
                                    name="password"
                                    type="password"
                                    v-model="password"
                            />
                        </v-form>
                    </v-card-text>
                    <v-card-actions>
                        <v-spacer />
                        <v-btn action="#" @click="login" color="primary">Ingresar</v-btn>
                    </v-card-actions>
                </v-card>

            </v-col>
        </v-row>
    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex';

    export default {


      name: 'login',
        data: () => ({
            username: '',
            password: '',
            messageError: 'El rut o contraseña ingresados son incorrectos.',
            messageBan: 'Esta cuenta se encuentra inactiva, contacte al administrador.'
        }),

        computed: {
            ...mapState(['issueLogIn'])},

        methods: {
            ...mapMutations(['issueLogInFunct']),

            login() {
                // eslint-disable-next-line no-console
                //console.log("miau")
                this.$store.dispatch('retrieveToken', {
                    rut: this.username,
                    password: this.password
                })
                /*   .then(response => {
                   //this.$router.push(name: 'todo')
               })
               */
            }
        }
    }

</script>