<template>
    <div style="float:center; position: relative;">
        <v-spacer></v-spacer>
        <!-- Tabla de supervision Matemáticas -->
        <v-card max-width="60%" style="float:center; position: relative; left:20%" >
            <h4  style="text-align: center; color:white;background-color:#191970">Tabla discrepancias correctores</h4>
            <v-card-title>
                <v-row style="text-align: center">
                    <v-col cols="6" style="text-align: center; justify-content: center; margin-left: 27%">
                        <v-text-field
                                v-model="search"
                                append-icon="search"
                                label="Ingrese el rut del corrector que desea buscar"
                                single-line
                                hint="Ingrese el rut con puntos y guión. Ejemplo: 10.000.000-1 o 1.000.000-1" 

                        />
                    </v-col>
                </v-row>
            </v-card-title>

            <v-data-table dense
                          :headers="headersCorrectors"
                          :items="this.content"
                          :search="search"
                          :items-per-page="15"
                          
            >

            <template v-slot:item.discrepancyPercent="{ item }">
                <v-chip style=" display: table; margin: 0 6% 0% 20%; width:50px; text-align:center" :color="getColor(item.discrepancyPercent)" dark>{{ item.discrepancyPercent }}</v-chip>
            </template> 

            </v-data-table>
            
        </v-card>

        <v-card> </v-card>


    </div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex';
    export default {
        data() {
            return {
                content: null,
                search: '',

                headersCorrectors: [
                    {text: 'Rut', value: 'rut'},
                    {text: 'Nombre', value: 'name'},
                    {text: 'Rol de corrección', value: 'correctionRol'},
                    {text: 'Doble revisiones', value: 'doubleRevisions'},
                    {text: 'Discrepancias', value: 'discrepancy'},
                    {text: 'Discrepancias (%)', value: 'discrepancyPercent'},
                ],
            }
        },
        computed:
            {
                ...mapState(['allStudents','correctorsTableWriting','correctorsTableMath','tableSelection','serverRoute']),

            },

        beforeMount: function () {
            if(this.tableSelection === "Escritura")
            {
                this.content = this.correctorsTableWriting 
            }

            else
            {
                this.content = this.correctorsTableMath 
            }
        },

        methods: {
            ...mapMutations(['changeViewItemTable']),
            getColor(discrepancyPercent)
            {
                // eslint-disable-next-line no-console
                //console.log(discrepancyPercent);
                if (discrepancyPercent == 0) 
                {
                    return 'green'   
                }
                else if (discrepancyPercent <= 10 && discrepancyPercent > 0) {
                    return 'orange'
                }
                else
                {
                    return 'red'
                }
            }
        },


        watch: {
            correctorsTable: function () {
                this.content = this.correctorsTable
            }
        }
    }

</script>

