<template>
  <v-app id="inspire">
    <v-app-bar v-if="tokenAuxLog != null"
            app
            color="#191970"
            height="100%"
            dark
			
	>	<v-row>
			<v-col cols="6" >
				<v-btn @click="changeShowTableAux" class="mt-0" text to='/'><v-toolbar-title >Plataforma de evaluación</v-toolbar-title></v-btn>
			</v-col>

			<v-col cols="4">
				<div style="margin-left:20%" class="mt-2">
					<v-icon  class="pt-0">mdi-account-circle</v-icon>
					<span style=""> {{userInfoTemp.name}} {{userInfoTemp.last_name}}</span> 
					<v-menu :close-on-content-click= closeNotifications offset-y v-if="userInfoTemp.plataformRol == 1 && showTable == true" persistend>
						<template v-slot:activator="{ on }">
							<v-btn
							v-on="on"
							style="background:0; width:auto; height:100%;margin-left:50px"
							class="pl-0 pr-0"
							text
							>
							<h5 class="ml-2">Notificaciones</h5> <v-icon medium class="ml-3" style="color:#2196F3 !important">mdi-bell</v-icon>
							</v-btn>
						</template>
						<v-list three-line style="max-height:400px; overflow-y:scroll; overflow-x:hidden; max-width:700px; min-width:700px">
							<div v-if="items == 0 || items == 1">No hay notificaciones</div>
							<v-btn @click="localShowNotifications" text style="width:50%; margin-top:20px; height:20%; font-size:12px; color:#191970;"> Ver todas las notificaciones <v-icon color="#2196F3"> mdi-notification-clear-all </v-icon> </v-btn>
							<v-btn @click="generateNotificationsLocal" text style="width:50%; margin-top:20px; height:20%; font-size:12px; color:#191970;"> Generar notificaciones <v-icon  color="#2196F3"> mdi-refresh </v-icon> </v-btn>
							<template v-for="(item, index) in items">
								<v-subheader
										v-if="item.header"
										:key="item.header"
										v-text="item.header"
								/>

								<v-divider
										v-else-if="item.divider"
										:key="index"
										:inset="item.inset"
								/>

								<v-list-item
								v-else
								:key="item.title"
								dense
								>
									<v-list-item-avatar>
										<v-icon style="color:#191970 !important">email</v-icon>
									</v-list-item-avatar>

									<v-list-item-content>
										<v-list-item-title>{{item.title}} </v-list-item-title>
										<v-list-item-subtitle class="mb-3 mt-2">
											Generada el: {{item.date}}
										</v-list-item-subtitle>
										<v-list-item-subtitle >
											<v-btn @click="ignoreNotificationLocal(item.id,index)" text style="height:50%;  font-size:12px; color:#191970;" class="pl-0">Ignorar notificación</v-btn> 
											<v-btn @click.stop="showDialog(item,index)" text style=" height:50%; font-size:12px; color:red;">Eliminar producción</v-btn>
										</v-list-item-subtitle>
									</v-list-item-content>
								</v-list-item>
							</template>
							</v-list>
					</v-menu>

					<v-menu
					v-model="menu"
					:close-on-content-click="false"
					:nudge-width="300"
					offset-y
					v-if="userInfoTemp.plataformRol == 2"
					>
					<template v-slot:activator="{ on }">
						<v-btn
						color="white"
						
						v-on="on"
						style="margin-left:40px"
						text
						>
							<h5 class="ml-2"> Documentos </h5> <v-icon medium class="ml-3" style="color:#2196F3 !important">mdi-file-upload</v-icon>
						</v-btn>
					</template>

					<v-card style="margin-top:28px;">
						<h5 class="pt-5" style="text-align: center; color:#191970;"> Consolidado de correcciones <v-icon class="ml-1 mb-1" color="#2196F3">mdi-file-document-box-multiple</v-icon> </h5>

						<v-list>
						<v-list-item>
							<v-row>
								<v-col cols="1"></v-col>
								<v-col cols="5">
									<v-btn class="ma-2" style="width: 100%" tile outlined color="#191970" @click="getMathResume"><v-icon left color="#191970">mdi-file-download-outline</v-icon> Matemáticas</v-btn>
									</v-col>
								<v-col cols="5">
									<v-btn class="ma-2" style="width: 100%" tile outlined color="#191970" @click="getWritingResume"><v-icon left color="#191970">mdi-file-download-outline</v-icon> Escritura</v-btn>
									</v-col>
							</v-row>
						</v-list-item>
						</v-list>

						<v-card-actions>
						<v-spacer></v-spacer>

						</v-card-actions>
						<v-divider></v-divider>
						<v-divider></v-divider>
						<v-divider></v-divider>
						<v-divider></v-divider>
						<v-divider></v-divider>
						<v-divider></v-divider>
						<v-divider></v-divider>


						<h5 class="pt-5" style="text-align: center; color:#191970;">Carga masiva de archivos <v-icon class="ml-1 mb-1" color="#2196F3">mdi-account</v-icon> </h5>

						<v-list>
						<v-list-item>
							<v-file-input
								v-model="userFile"
								color="#191970"
								counter
								label="Excel Usuarios"
								placeholder="Cargar archivo"
								prepend-icon="mdi-paperclip"
								flat
								:show-size="1000"
								class="mt-5"
								accept=".xlsx"
								ref="userFile"
								type="file"

							>
								<template v-slot:selection="{ index, text }">
								<v-chip
									v-if="index < 2"
									color="#191970"
									dark
									label
									small
								>
									{{ text }}
								</v-chip>
								</template>
							</v-file-input>
						</v-list-item>
						</v-list>

						<v-list>
						<v-list-item>
							<v-file-input
								v-model="studentFile"
								color="#191970"
								counter
								label="Excel Estudiantes"
								placeholder="Cargar archivo"
								prepend-icon="mdi-paperclip"
								:show-size="1000"
								class="mt-5"
								accept=".xlsx"

							>
								<template v-slot:selection="{ index, text }">
								<v-chip
									v-if="index < 2"
									color="#191970"
									dark
									label
									small
								>
									{{ text }}
								</v-chip>

								<span
									v-else-if="index === 2"
									class="overline grey--text text--darken-3 mx-2"
								>
									+{{ files.length - 2 }} File(s)
								</span>
								</template>
							</v-file-input>
						</v-list-item>
						</v-list>

						<v-card-actions>
						<v-spacer></v-spacer>
						<v-row>
							<v-col cols="1"></v-col>
							<v-col cols="3">	<v-btn class="mt-2" text @click="menu = false">Cancelar</v-btn></v-col>
							<v-col cols="3">	<v-btn class="mt-2" :disabled="studentDisabled" color="primary" text @click="bulkDialog = true">Enviar</v-btn></v-col>
						</v-row>
						</v-card-actions>
					</v-card>
					</v-menu>

					<v-dialog v-model="bulkDialog" max-width="420">
						<v-card>
							<v-card-title class="headline">¿Desea cargar estos archivos con los nuevos estudiantes y correctores del sistema?</v-card-title>
							<v-card-text> Recuerde que al realizar esta acción es crítica, ya que se eliminarán todos los estudiantes 
										y correctores que antes estuviesen registrados en el sistema, lo que incluye las evaluaciones de estos. 
							</v-card-text><v-card-actions>
							<v-spacer></v-spacer>
							<v-btn color="green darken-1" text @click="bulkDialog = false">No guardar</v-btn>
							<v-btn color="red darken-1" text @click="bulkDialog = false, studentFileUpload(), userFileUpload()">Sí, guardar</v-btn>
							</v-card-actions>
						</v-card>
					</v-dialog>

					<v-btn v-if="userInfoTemp.plataformRol === 0 && userInfoTemp.correctionRol === 0" @click="localChangeShowTableAux"
							style="background:0; width:auto; height:100%;margin-left:40px"
							class="pl-0 pr-0"
							text
					>
						<h5 class="ml-2">Tercera corrección</h5> <v-icon medium class="ml-3" style="color:#2196F3 !important">mdi-table-large</v-icon>
					</v-btn>

					<v-btn
							style="background:0; width:auto; height:100%; margin-left:60px "
							class="pl-0 pr-0"
							text
							@click="changeViewItemTable"
							v-else-if="showTable === false && userInfoTemp.plataformRol === 1"
							>
							<h5 class="ml-2">Ver tabla</h5> <v-icon medium class="ml-3" style="color:#2196F3 !important">mdi-table-large</v-icon>
						</v-btn>
				</div>

				<v-dialog v-if="currentItem != null" v-model="dialog" persistent max-width="400"> 
				<v-card>
					<v-card-title class="headline">¿Desea borrar la producción?</v-card-title>
					<v-card-text>Se eliminará la producción del evaluador con rut {{currentItem.rutCorrector}} del día {{currentItem.date}}</v-card-text>
					<v-card-actions>
					<v-spacer></v-spacer>

					<v-btn color="#191970" text @click="dialog = false">Cancelar</v-btn>
					<v-btn color="red" @click="eraseProductionLocal(currentItem), dialog = false" text>Aceptar</v-btn>

					</v-card-actions>
				</v-card>
			</v-dialog>
											

			</v-col>
			<v-col cols="2" >
				<v-btn style="width:auto; height:80%" class="pl-0 pr-0 mt-2" text @click="logOutFunct"> <h5 class="ml-2 mr-2">Cerrar sesión</h5>  <v-icon medium class="ml-2" style="color:#2196F3 !important">mdi-exit-to-app</v-icon></v-btn>
			</v-col>
		</v-row>
    </v-app-bar>

	<v-content>
		<v-container fluid>
			<router-view/>
		</v-container>
	</v-content>

  </v-app>
</template>


<script>

	import axios from 'axios';
	import {mapState, mapMutations} from 'vuex';
	export default {
		data: () => ({
			tokenAuxLog: null,
			userInfoTemp: null,
			items: null,
			dialog: false,
			closeNotifications: false,
			currentItem: null,
			currentIndex: null,
			menu: false,
			bulkDialog: false,
			userFile: null,
			studentFile: null,
			userDisabled: true,
			studentDisabled: true,
			file: null,
		}),
		created:function () {
			this.tokenAuxLog = sessionStorage.getItem('acces_token')
			this.userInfoTemp = JSON.parse(sessionStorage.getItem('infoUser'))
			if( this.tokenAuxLog != null)
			{
				//this.$store.dispatch('getAllStudents')
				this.$store.dispatch('getActiveStudents')

				if(this.userInfoTemp.plataformRol == 1)
				{
					this.$store.dispatch('getNonReadedNotifications')
				}

			}
		},
		beforeCreate()	
		{


		},
		watch: 
		{
			nonReadedNotifications: function()
			{
				this.items = this.nonReadedNotifications
			},
			userFile: function()
			{
				if (this.userFile != null && this.studentFile != null)
				{
					this.studentDisabled = false	
				}

				else
				{
					this.studentDisabled = true
				}
			},
			studentFile: function()
			{
				if (this.userFile != null && this.studentFile != null)
				{
					this.studentDisabled = false	
				}

				else
				{
					this.studentDisabled = true
				}
			}
		},
		computed: {
		...mapState(['logIn', 'usrInfoLogIn','nonReadedNotifications','showTable'])
		},

		methods: {
			...mapMutations(['userBulkUpload', 'studentBulkUpload', 'logOutFunct','ignoreNotification','eraseProduction','showNotificationsFunct', 'showTableFunct', 'changeViewItemTable', 'changeShowTableAux','getMathResume','getWritingResume']),
			userFileUpload()
			{
				
				let formData = new FormData();
				formData.append('file', this.userFile)				
				this.userBulkUpload(formData)
				this.$store.dispatch('getAllUsers')
			},
			studentFileUpload()
			{
				let formDataS = new FormData();
				formDataS.append('file', this.studentFile)
				this.studentBulkUpload(formDataS)
				this.$store.dispatch('getAllStudents')
			},
			showDialog(item,index)
			{
				this.dialog = true;
				this.currentItem = item;
				this.currentIndex = index; 

			},
			sleep (time) {
                return new Promise((resolve) => setTimeout(resolve, time));
            },
			generateNotificationsLocal()
			{
				this.closeNotifications = false
				this.$store.dispatch('generateNotifications')
				this.sleep(200).then(() => {
					this.$store.dispatch('getNonReadedNotifications')
					this.$store.dispatch('getAllNotifications')
				});
			},
			eraseProductionLocal(item){
				this.eraseProduction(item)
				this.closeNotifications = false
				this.dialog = false
				this.$delete(this.nonReadedNotifications, this.currentIndex)
				this.$store.dispatch('getNonReadedNotifications')
				this.$store.dispatch('getProductionDeleted')
				this.$store.dispatch('getAllNotifications')
				this.$store.dispatch('getAllStudents');

			},
			ignoreNotificationLocal(id,index)
			{
				this.ignoreNotification(id)
				this.$delete(this.nonReadedNotifications, index)
				this.$store.dispatch('getNonReadedNotifications')
				this.$store.dispatch('getAllNotifications')
				this.closeNotifications = false


			},
			localShowNotifications()
			{
				this.closeNotifications = true
				this.showNotificationsFunct();
			},
			localChangeShowTableAux()
			{
				//this.$store.dispatch('getAllStudents')
				this.changeShowTableAux();
			},
			download(){
				axios({
					url: this.serverRoute + '/user/getwritresume',
					method: 'GET',
					responseType: 'blob', // important
				}).then((response) => {
					const url = window.URL.createObjectURL(new Blob([response.data]));
					const link = document.createElement('a');
					link.href = url;
					link.setAttribute('download', 'file.xlsx'); //or any other extension
					document.body.appendChild(link);
					link.click();
				});
			}
		}
	}

</script>


<style>
</style>