<template>
    <div>
        <v-row>
            <v-col cols="2"></v-col>
            <v-col cols="7">
                <v-card
                        class="pa-2"
                        outlined
                        tile
                        style="width:60%; margin-left:30%"
                >
                    <div style="">
                        <v-select
                                :items="items"
                                label="Seleccione qué desea administrar"
                                v-model="selection"
                        />
                    </div>
                </v-card>
            </v-col>
            <v-col cols="3"></v-col>

        </v-row>

        <v-row v-if="showAdminUser">
            <v-col cols="1"></v-col>
            <v-col cols="10">
                <User></User>
            </v-col>
            <v-col cols="1"></v-col>
        </v-row>
        
        <v-row v-else-if="showAdminStudent">
            <v-col cols="1"></v-col>
            <v-col cols="10">
                <Student></Student>
            </v-col>
        </v-row>
    </div>
</template>

<script>
//Vistas del usuario administrador
import User from '../admin/User.vue'
import Student from '../admin/Student.vue'
import {mapState, mapMutations} from 'vuex';
export default {
    data: () => ({
        items: ["Usuarios", "Estudiantes"],
        selection: "Estudiantes"
    }),
    components:
    {
        User,
        Student
    },
    computed: {
		...mapState(['showAdminUser','showAdminStudent'])
	},
    methods:
    {
        ...mapMutations(['showAdminTables']),
    },
    watch:
    {
        selection()
        {
            this.showAdminTables(this.selection)
        }
    }
}
</script>