                let large = this.listScores.length;
                let contador = 0;
                this.listS = [];

                for (let j=0; j<this.array1S.length; j++){
                    for(let i=0; i<large; i++){
                        if (this.array1S[j] === this.listScores[i].scores[1].scoreObtained[0]){
