                if (lengthS === 0){
                    for (let i = 0; i < 3; i++) {
                        this.percentageS.push(0)
                    }
                }
                else {
                    for (let i = 0; i < this.totalMatS.length; i++) {
                        var maxS = Math.max.apply(Math, this.totalMatS[i]);

                        let percentS = 0;
                        percentS = ((lengthS - maxS) / lengthS) * 100;
                        this.percentageS.push(this.dosDecimales(percentS))
                    }
