            calcPercentageE() {

                let lengthS = this.listScoresE.length;
                let length = this.listRecorrectionE.length;


                // eslint-disable-next-line no-console
                //console.log("el largo es. ", length);

                if (lengthS === 0){
                    for (let i = 0; i < 8; i++) {
                        this.percentageSE.push(0)
                    }
                }

                else {
                    for (let i = 0; i < this.totalMatSE.length; i++) {
                        var maxS = Math.max.apply(Math, this.totalMatSE[i]);

                        let percentS = 0;

                        percentS = ((lengthS - maxS) / lengthS) * 100;
                        this.percentageSE.push(this.dosDecimales(percentS))
                    }
                }

                if (length === 0){
                    for (let i = 0; i < 8; i++) {
                        this.percentageRE.push(0)
                    }
                }
                else {

                    for (let i = 0; i < this.totalMatRE.length; i++) {
                        var max = Math.max.apply(Math, this.totalMatRE[i]);
                        // eslint-disable-next-line no-console
                        //console.log("el maximo es. ", max);

                        let percent = 0;

                        // eslint-disable-next-line no-console
                        //console.log("numerador ", (length - max));
                        // eslint-disable-next-line no-console
                        //console.log("denomin ", (length));
                        percent = ((length - max) / length) * 100;
                        this.percentageRE.push(this.dosDecimales(percent))
                    }
                }
            },

            //extra
            dosDecimales(n) {
                let t = n.toString();
                let regex = /(\d*.\d{0,2})/;
                return t.match(regex)[0];

            }
        },

