<template>
		<v-data-table
			:headers="headers"
			:search="search"
			:items="users"
			loading="load"
			loading-text="Cargando"
			class="elevation-3 	"
		>
			<template v-slot:top>
			<v-toolbar flat color="#191970">
				<v-toolbar-title style="color:white">Usuarios</v-toolbar-title>
				<v-divider
				class="mx-4"
				inset
				style="background-color:white"
				vertical
				></v-divider>
				<v-text-field
					v-model="search"
					append-icon="search"
					label="Buscar usuario por rut"
					dark
					style="margin-left:13%; margin-right:11%"
					single-line
					hint="Ingrese el rut con puntos y guión. Ejemplo: 10.000.000-1 o 1.000.000-1"
				></v-text-field>
				<v-spacer></v-spacer>
				<v-dialog v-model="dialog" max-width="600px">
					<template v-slot:activator="{ on }">
						<v-btn outlined text color="white" dark rounded v-on="on"> Agregar <v-icon color="blue" class="ml-3">mdi-account-plus-outline</v-icon> </v-btn>
					</template>
					<v-card>
						<v-card-title>
						<span class="headline">{{ formTitle }}</span>
						</v-card-title>

						<v-card-text class="pb-0">
						<v-container class="pb-0">
							<v-form v-model="valid" ref="form" lazy-validation class="pb-0">
								<v-row>
								<v-col cols="12" sm="6" md="4" v-if="editedIndex != -1">
									<v-text-field 
									v-mask="['#.###.###-X','##.###.###-X']"
									hint="Ejemplo: 11.111.111-1 o 1.111.111-1" 
									required 
									v-model="editedItem.rut" 
									label="Rut"
									disabled
									:rules="rutRules"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4" v-if="editedIndex == -1">
									<v-text-field 
									v-mask="['#.###.###-X','##.###.###-X']"
									hint="Ejemplo: 11.111.111-1 o 1.111.111-1" 
									required 
									v-model="editedItem.rut" 
									label="Rut"
									:rules="rutRules"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
									<v-text-field v-model="editedItem.name" label="Nombre" required :rules="textRules" ></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
									<v-text-field v-model="editedItem.last_name" label="1° Apellido" required :rules="textRules"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
									<v-text-field v-model="editedItem.second_name" label="2° Apellido" required :rules="textRules"></v-text-field>
								</v-col>
								<v-col cols="12" sm="6" md="4">
									<v-select
										v-model="editedItem.plataformRol"
										:items="plataformOptions"
										label="Rol plataforma"
										item-text="name"
										item-value="value"
										:rules="plataformRules"
										required
									></v-select>
								</v-col>
								<v-col cols="12" sm="6" md="4">
									<v-select
										v-model="editedItem.correctionRol"
										:items="correctionOptions"
										:disabled="disabledCorrectionRol"
										item-text="name"
										item-value="value"
										label="Rol corrección"
										:rules="correctionRules"
										required
									></v-select>
								</v-col>
								</v-row>
								<v-row v-if="editedIndex != -1">
									<v-col cols="12" sm="6" md="4">
										<v-text-field 
											v-model="editedItem.password"
											:append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
											:rules="textRules"
											:type="show ? 'text' : 'password'"
											name="input-10-1"
											label="Contraseña"
											@click:append="show = !show"
										></v-text-field>
									</v-col>
								</v-row>
							</v-form>
						</v-container>
						</v-card-text>

						<v-card-actions class="pt-0">
							<v-spacer></v-spacer>
							<v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
							<v-btn color="blue darken-1" text @click="save(formTitle)">Guardar</v-btn>
						</v-card-actions>
					</v-card>
				</v-dialog>
			</v-toolbar>
			</template>

			<template v-slot:item.correctionRol="{ item }">
				<p v-if="item.correctionRol == 0 && item.plataformRol == 0"> Escritura </p>
				<p v-else-if="item.correctionRol == 1 && item.plataformRol == 0"> Matemática </p>
				<p v-else> </p>
			</template>

			<template v-slot:item.plataformRol="{ item }">
				<p v-if="item.plataformRol == 0"> Corrector </p>
				<p v-else-if="item.plataformRol == 1"> Supervisor </p>
				<p v-else> Administrador </p>
			</template>

			<template v-slot:item.active="{ item }">
				<v-chip color="red" v-if="item.active == false"> <v-icon x-small color="white">mdi-close</v-icon>  </v-chip>
				<v-chip color="green" v-else-if="item.active == true"> <v-icon x-small color="white">mdi-check</v-icon></v-chip>
			</template>

			<template v-slot:item.action="{ item }">
			<v-tooltip bottom>
				<template v-slot:activator="{ on }">
					<v-icon
						small
						class="mr-2"
						@click="editItem(item)"
						v-on="on"
					>
						edit
					</v-icon>					
				</template>	
				<span> Editar </span>
			</v-tooltip>

			<v-tooltip bottom>
				<template v-slot:activator="{ on }">
					<v-icon
						small
						class="mr-2"
						@click="deleteItem(item)"
						v-on="on"
					>
						mdi-swap-horizontal-bold
					</v-icon>					
				</template>	
				<span> Cambiar estado </span>
			</v-tooltip>

			</template>
			<template v-slot:no-data>
			<v-btn color="primary" @click="initialize">Reset</v-btn>
			</template>
		</v-data-table>
</template>

<script>
  import {mapState,mapMutations}  from 'vuex'
  export default {
    data: () => ({
		correctionOptions: [{value:0,name:'Escritura'}, {value:1,name:'Matemática'}],
		plataformOptions: [{value:0,name:'Evaluador'}, {value:1,name:'Supervisor'}, {value:2,name:'Administrador'}], 
		valid : true,
		search: '',
		dialog: false,
		load: true,
		disabledCorrectionRol: true,
		show: false,
		headers: [
			{
				text: 'Nombre',
				align: 'center',
				value: 'name',
			},
			{ text: '1° Apellido', value: 'last_name', align: 'center' },
			{ text: '2° Apellido', value: 'second_name', align: 'center' },
			{ text: 'Rut', value: 'rut', align: 'center' },
			{ text: 'Corrección', value: 'correctionRol', align: 'center' },
			{ text: 'Plataforma', value: 'plataformRol', align: 'center' },
			{ text: 'Estado', value: 'active', align: 'center' },
			{ text: 'Acciones', value: 'action', sortable: false },
		],
		rutRules: [
			v => !!v || 'Campo requerido',
			v => (v && v.length >= 11 && v.length <= 12) || 'Rut debe tener formato X.XXX.XXX-X o XX.XXX.XXX-X '
		],
		textRules: [
			v => !!v || 'Campo requerido'
		],
		correctionRules:[],
		plataformRules:
		[
			v => !!v || 'Campo requerido'
		],
		users: [],
		editedIndex: -1,
		editedItem: {
			name: '',
			last_name: '',
			second_name: '',
			rut: '',
			correctionRol: '',
			plataformRol: '',
			active: ''
		},
		defaultItem: {
			name: '',
			last_name: '',
			second_name: '',
			rut: '',
			correctionRol: '',
			plataformRol: '',
			active: ''

		},
    }),

    computed: {
		...mapState(['allUsers']),
		formTitle () {
			return this.editedIndex === -1 ? 'Nuevo usuario' : 'Editar usuario'
		},
		plataformRol()
		{
			return this.editedItem.plataformRol
		},
		correctionRol()
		{
			return this.editedItem.correctionRol
		}
    },

    watch: {
		dialog (val) {
			val || this.close()
		},
		
		allUsers: function()
		{
			this.users = this.allUsers 
			this.load = false
		},
		plataformRol: function()
		{

			// eslint-disable-next-line no-console
			//console.log(this.editedItem)
			if (this.editedItem.plataformRol === 0)
			{
				this.plataformRules =  []
				this.disabledCorrectionRol = false
				this.correctionRules = [v => !!v || 'Campo requerido']
			}
			else
			{
				this.disabledCorrectionRol = true
				this.plataformRules =  [v => !!v || 'Campo requerido']
				this.correctionRules = []
			}
		},
		correctionRol: function()
		{
			if (this.editedItem.correctionRol === '' || this.editedItem.correctionRol === 0) {
				this.correctionRules = []
			}
			else 
			{
				this.correctionRules = [v => !!v || 'Campo requerido']
			}
		}
    },

    created () {
      this.$store.dispatch('getAllUsers');
    },

    methods: {
		...mapMutations(['addNewUser','editUser','changeUserState']),
		editItem (item) {
			this.editedIndex = this.users.indexOf(item)
			this.editedItem = Object.assign({}, item)
			this.dialog = true
			// eslint-disable-next-line no-console
			//console.log("Soy item",item)
		},
		deleteItem (item) {
			const index = this.users.indexOf(item)
			if (item.active == true) 
			{
				confirm('¿Seguro que desea deshabilitar al usuario ' + item.name + '?') && this.changeUserState({user:item, index:index})

			}
			else
			{
				confirm('¿Seguro que desea volver a habilitar al usuario ' + item.name + '?') && this.changeUserState({user:item, index:index})
			}
		},
		close () {
			this.dialog = false
			setTimeout(() => 
			{
				this.editedItem = Object.assign({}, this.defaultItem)
				this.editedIndex = -1
			}, 300)
		},

		save (formTitle) {
			if (this.$refs.form.validate()) 
			{
				if (formTitle == "Nuevo usuario") 
				{
					this.editedItem.correctionRol = 0
					this.editedItem.active = true
					this.addNewUser(this.editedItem)
					
				}
				else
				{
					
					this.editUser({user:this.editedItem, index: this.editedIndex})
				}
				this.snackbar = true
				this.close()
			}
		},
	},
  }
</script>

