import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import TableAux from "./components/supervisor/TableAux";

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'home',
            component: Home
        },
        {
            path: '/tableWrt',
            name: 'table',
            component: TableAux
        }
    ]
})