import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';
import router from './router'

Vue.use(Vuex, axios, router);

export default new Vuex.Store({
    state: {
        token: sessionStorage.getItem('acces_token') || null,
        currentStudent: '',
        //formulario de evaluacion
        showEvaluation: false,
        showAdd: false,
        activeStudents: [],
        allStudents: [],
        lastStudent: '',
        logIn: false,
        usrInfoLogIn: '',
        issueLogIn: 0, // 0 sin error,  1 error 1, 2 error 2, 3 error 3 (ban)
        nonReadedNotifications: null,
        allNotifications: null,
        productionDeleted: null,
        showNotifications: false,
        showTable:true,
        //tabla corrector escritura
        showTableAux: false,
        showItemTable: false,
        tableSelection: '',
        correctorsTableWriting: null,
        correctorsTableMath: null,
        allUsers: [],
        showAdminUser: false,
        showAdminStudent: true,
        serverRoute: "http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT",
        //serverRoute: 'http://localhost:8081'
    },
    mutations: {
        getMathResume()
        {
            axios({
                url: 'http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT' + '/user/getmathresume',
                method: 'GET',
                responseType: 'blob',
            }).then((response) => {
                 var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                 var fileLink = document.createElement('a');
              
                 fileLink.href = fileURL;
                 fileLink.setAttribute('download', 'matematica.xlsx');
                 document.body.appendChild(fileLink);
               
                 fileLink.click();
            });
        },
        getWritingResume()
        {
            axios({
                url: 'http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT' + '/user/getwritingresume',
                method: 'GET',
                responseType: 'blob',
            }).then((response) => {
                 var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                 var fileLink = document.createElement('a');
              
                 fileLink.href = fileURL;
                 fileLink.setAttribute('download', 'escritura.xlsx');
                 document.body.appendChild(fileLink);
               
                 fileLink.click();
            });
        },
        generateNotifications()
        {
            try {
                axios.get('http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT' +  '/notifications/generate/')
                    .then(response => {
                        // eslint-disable-next-line no-console
                        console.log(response)

                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        showAdminTables(state, value)
        {
            if(value === "Usuarios")
            {
                state.showAdminStudent = false,
                state.showAdminUser = true
            }
            else
            {
                state.showAdminStudent = true,
                state.showAdminUser = false
            }
        },
        studentBulkUpload(state, file){
            // eslint-disable-next-line no-console
            //console.log("entro al post");
            axios.post(state.serverRoute + '/student/bulkupload/',file, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
                .then(response => {
                    state.allStudents = response.data
                    // eslint-disable-next-line no-console
                    console.log(response);
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                })
        },
        userBulkUpload(state,file)
        {
            axios.post(state.serverRoute + '/user/bulkupload/',file, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            })
            .then(response => {
                state.allUsers = response.data
                // eslint-disable-next-line no-console
                console.log(response);
            })
            .catch(error => {
                // eslint-disable-next-line no-console
                console.log(error)
            })
        },
        addNewUser(state, user)
        {                        
            axios.post(state.serverRoute + '/user/create/',user)
                    .then(response => {
                        // eslint-disable-next-line no-console

                        state.allUsers.push(user)
                        // eslint-disable-next-line no-console
                        console.log(response);
                    })
                    .catch(error => {
                        // eslint-disable-next-line no-console
                        console.log(error)
                    })
        },
        changeUserState(state,data)
        {                        
            axios.post(state.serverRoute + '/user/changestate/', data.user)
                .then(response => {
                    Object.assign(state.allUsers[data.index], response.data)
                    // eslint-disable-next-line no-console
                    console.log(response);
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                })
        },

        editUser(state, data)
        {
            axios.post(state.serverRoute + '/user/edit/',data.user)
                .then(response => {
                    Object.assign(state.allUsers[data.index], response.data)
                    // eslint-disable-next-line no-console
                    console.log(response);
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                })
        },
        getCorrectorsTableWriting(state)
        {
            axios.get(state.serverRoute + '/user/correctorstablewriting/')
                .then(response => {
                    // eslint-disable-next-line no-console
                    console.log(response.data)
                    state.correctorsTableWriting = response.data
                    // eslint-disable-next-line no-console
                    //console.log("La data", state.correctorsTable)
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                    // eslint-disable-next-line no-console
                    //console.log("No estoy cargando la data", state.correctorsTable)

                })
        },
        getCorrectorsTableMath(state)
        {
            axios.get(state.serverRoute + '/user/correctorstablemath/')
                .then(response => {
                    // eslint-disable-next-line no-console
                    console.log(response.data)
                    state.correctorsTableMath = response.data
                    // eslint-disable-next-line no-console
                    //console.log("La data", state.correctorsTable)
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                    // eslint-disable-next-line no-console
                    //console.log("No estoy cargando la data", state.correctorsTable)

                })
        },
        changeTableSelection(state, subject){
            state.tableSelection = subject
            // eslint-disable-next-line no-console
            //console.log("el state vale: ", state.tableSelection)
        },

        changeViewItemTable(state){
            state.showTable = !state.showTable;
            state.showItemTable = !state.showItemTable;
            if (state.showNotifications===true){
                state.showItemTable = false;
                state.showTable = true;
            }
            state.showNotifications = false
        },

        changeShowTableAux(state){
            state.showTableAux = !state.showTableAux
            state.showEvaluation = false
        },

        changeCurrentStudent(state, student) {
            state.currentStudent = student
        },
        showNotificationAux(state)
        {
            state.showNotifications = !state.showNotifications
        },
        showNotificationsFunct(state)
        {
            state.showNotifications = true
            state.showTable = false
        },
        showTableFunct(state)
        {
            state.showNotifications = false
            state.showTable = true
        },
        ignoreNotification(state,id)
        {
            axios.post(state.serverRoute + '/notifications/ignore/', {
                    id: id ,
                })
                .then(response => {
                    // eslint-disable-next-line no-console
                    //console.log(id);
                    // eslint-disable-next-line no-console
                    console.log(response);
                })
                .catch(error => {
                    // eslint-disable-next-line no-console
                    console.log(error)
                    // eslint-disable-next-line no-console
                    //console.log(id);
                })
        },
        changeLastStudent(state, student) {
            state.lastStudent = student
        },

        showEvaluationFunct(state, option) {
            state.showAdd = false
            state.showEvaluation = option // Puede ser true o false
        },
        eraseProduction(state, notification)
        {
            // eslint-disable-next-line no-console
            //console.log("Soy notificación",notification)
            try {
                axios.post(state.serverRoute + '/notifications/delete/' , notification)
                    .then(response => {
                        // eslint-disable-next-line no-console
                        console.log("Producción Eliminada", response.data)
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        getNonReadedNotifications(state)
        {
            try {
                axios.get(state.serverRoute+'/notifications/nonreaded/')
                    .then(response => {
                        state.nonReadedNotifications = response.data;
                        // eslint-disable-next-line no-console
                        console.log("Non readed notifications", state.nonReadedNotifications)
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        getProductionDeleted(state)
        {
            try {
                axios.get(state.serverRoute + '/notifications/productiondeleted/')
                    .then(response => {
                        state.productionDeleted = response.data;
                        // eslint-disable-next-line no-console
                        console.log("productionDeleted", state.productionDeleted)
                        // eslint-disable-next-line no-console
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        getAllNotifications(state)
        {
            try {
                axios.get(state.serverRoute + '/notifications/all/')
                    .then(response => {
                        state.allNotifications = response.data;
                        // eslint-disable-next-line no-console
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        getAllStudents(state) {
            try {
                axios.get(state.serverRoute + '/student/all/')
                    .then(response => {
                        state.allStudents = response.data;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },

        getActiveStudents(state){
            try {
                axios.get(state.serverRoute + '/student/getActive/')
                    .then(response => {
                        state.activeStudents = response.data;
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },

        getAllUsers(state) {
            try {
                axios.get('http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT/user/getall/')
                    .then(response => {
                        state.allUsers = response.data;
                        // eslint-disable-next-line no-console
                        console.log("users", state.allUsers)
                    })
                    .catch(e => {
                        this.errors.push(e)
                    })
                // eslint-disable-next-line no-console
            } catch (err) {
                // eslint-disable-next-line no-console
                console.log("En get all all " + err)
            }
        },
        showAddStudentFunct(state, option) {
            state.showEvaluation = false
            state.showAdd = option
        },
        retrieveToken(state, token) {
            state.token = token
        },
        retrieveLogIn(state, data) {

            //const tokenAux = sessionStorage.getItem('acces_token')
            // eslint-disable-next-line no-console
            //console.log("el tokenAux vale: ", tokenAux)
            if (state.token == null) {
                // eslint-disable-next-line no-console
                //console.log("el token es nulo")
            } else {
                state.logIn = true
                state.usrInfoLogIn = data
                // eslint-disable-next-line no-console
                //console.log("state vale: ", state.usrInfoLogIn)
                sessionStorage.setItem('infoUser', JSON.stringify(state.usrInfoLogIn))
                location.reload()
            }
        },

        logOutFunct(state) {
            state.logIn = false
            sessionStorage.removeItem('acces_token')
            // eslint-disable-next-line no-console
            //console.log("el token al removerlo vale: ", sessionStorage.getItem('acces_token'));
            sessionStorage.removeItem('infoUser')
            location.reload()
        },
        issueLogInFunct(state, option) {
            state.issueLogIn = option
        },
    },
    actions: {
        getAllUsers(context)
        {
            context.commit('getAllUsers');
        },
        getCorrectorsTableWriting(context)
        {
            context.commit('getCorrectorsTableWriting')
        },
        getCorrectorsTableMath(context)
        {
            context.commit('getCorrectorsTableMath')
        },
        generateNotifications(context)
        {
            context.commit('generateNotifications')
        },
        getActiveStudents(context){
            context.commit('getActiveStudents')
        },
        getAllStudents(context) {
            context.commit('getAllStudents')
        },
        getNonReadedNotifications(context)
        {
            context.commit('getNonReadedNotifications')
        },
        getAllNotifications(context)
        {
            context.commit('getAllNotifications')
        },
        getProductionDeleted(context)
        {
            context.commit('getProductionDeleted')
        },
        retrieveToken(context, credentials) {
            // eslint-disable-next-line no-console
            console.log(credentials);
            return new Promise((resolve, reject) => {
                axios.post('http://correctores.usach.cl:8080/evaluacion-0.0.1-SNAPSHOT' + '/user/login/', {
                    rut: credentials.rut,
                    password: credentials.password
                })
                    .then(response => {
                        const token = response.data.token;
                        sessionStorage.setItem('acces_token', token);
                        context.commit('retrieveToken', token);
                        resolve(response);
                        // eslint-disable-next-line no-console
                        console.log(response);

                        if (response.data.messageError === "Error 1" || response.data.messageError === "Error 2") {
                            // eslint-disable-next-line no-console
                            //console.log("Hay un error 1 o2 xd");
                            context.commit('issueLogInFunct', 1)
                            sessionStorage.removeItem('acces_token')
                        }
                        else if(response.data.messageError === "Error 3" ){
                            // eslint-disable-next-line no-console
                            //console.log("Hay un error 3 xd");
                            context.commit('issueLogInFunct', 3)
                            sessionStorage.removeItem('acces_token')
                        }
                        else {
                            // eslint-disable-next-line no-console
                            //console.log("entre :3");
                            context.commit('issueLogInFunct', 0);
                            const data = response.data;
                            context.commit('retrieveLogIn', data)
                        }
                    })
                    .catch(error => {
                        // eslint-disable-next-line no-console
                        console.log(error);
                        reject(error)
                    })
            })
        }
    }
})