<template>
     <v-row>
		<v-col cols="12" md="8">
			<v-card class="mx-auto" style="width: 80%; height: 100%">

				<v-row>
					<v-col >
					<h3 style="text-align: center">Buscar estudiante</h3>
					</v-col>	
				</v-row>

				<v-card-text>
					<v-autocomplete
					label="Estudiante"
					:items="activeStudents"
					item-text="rut"
					item-value="rut"
					v-model="value"
                    hint="Ingrese el rut con puntos y guión. Ejemplo: 10.000.000-1 o 1.000.000-1"
					:loading="isLoading"
					:placeholder="placeholderMsg"
					hide-no-data
					>

						<template v-slot:item="data">
							<template v-if="typeof data.item !== 'object'">
								<v-list-item-content v-text="data.item"></v-list-item-content>
							</template>
						<template v-else>
							<v-list-item-content>
							<v-list-item-title v-html="'Rut: ' + data.item.rut"></v-list-item-title>
							<v-list-item-subtitle v-html="'Nombre: ' + data.item.name"></v-list-item-subtitle>
							<v-list-item-subtitle v-html="'Apellido: ' + data.item.last_name"></v-list-item-subtitle>
							</v-list-item-content>
						</template>
						</template>
						

					</v-autocomplete>
					<v-card-actions style="justify-content: center">
						<v-btn style="float: right" text icon color="blue" @click="changeShow">
						<v-icon>mdi-magnify</v-icon>
						Buscar
						</v-btn>
					</v-card-actions>
					<v-card-actions style="justify-content: center">
						<v-btn style="float: right" text icon color="blue" @click="addShow">
							<v-icon>mdi-plus-circle</v-icon>
							Agregar estudiante
						</v-btn>
					</v-card-actions>
				</v-card-text>
			</v-card>
		</v-col>

		<v-col cols="12" md="8">
			<div>
				<v-alert style="width: 80%; margin-left: 10%" type="warning" dismissible v-model="issue">{{this.messageError}}</v-alert>
			</div>
		</v-col>
	
		<v-card v-if="showEvaluation"
				class="mx-aut"
				style="width: 30%; position:fixed; right:5%; top:20%">
			<v-list-item three-line>
			<v-list-item-content>
				<div class="overline mb-4">Perfil del estudiante</div>
				<v-list-item-title class="headline mb-1"> {{this.response.name}} {{this.response.last_name}} {{this.response.second_name}}</v-list-item-title>
				<v-list-item-subtitle class="mt-2">
				Rut: {{this.response.rut}}
				<br>
				Carrera: {{this.response.career}}</v-list-item-subtitle>
			</v-list-item-content>

			<v-list-item-avatar
					tile
					size="80"
			>
				<v-icon x-large color="blue" style="float: right">mdi-account</v-icon>
			</v-list-item-avatar>
			</v-list-item>
		</v-card>
</v-row>
</template>

<script>

import {mapMutations,mapState} from 'vuex';
import axios from 'axios';

export default {
    
    data: () => ({
		mask: '##.###.###-#',
		value: '',
		response: null,
		issue: false,
		messageError: "Debe ingresar un rut para realizar la búsqueda.",
		isLoading: false,
		search: null,
		placeholderMsg: "Rut del estudiante"

	}),
	computed:
	{
		...mapState(['lastStudent', 'showEvaluation', 'activeStudents','serverRoute'])
	},

	created: function(){
		if (this.activeStudents.length == 0)
		{
			this.isLoading = true	
			this.placeholderMsg = "Cargando Datos..."
		}
	},

	methods: {
		...mapMutations(['changeCurrentStudent', 'changeLastStudent', 'showEvaluationFunct', 'showAddStudentFunct']),


		changeShow() {
			//this.value = this.lastStudent;
			if (this.value === '') {
				this.issue = true
			} else {
				this.showAddStudentFunct(true)
				this.getStudent()
				this.value = ""
			}
		},
		addShow(){
			this.showAddStudentFunct(true)
		},

		getStudent() {
			try {
				axios.get(this.serverRoute + '/student/getRut/' + this.value)
						.then(response => {
							this.response = response.data;
							this.showEvaluationFunct(true);
						})
						.catch(e => {
							this.errors.push(e)
						})

			} catch (err) {
				// eslint-disable-next-line no-console
				console.log("En get all all " + err)
			}
		},
	},
		watch: {
			activeStudents: function () {
				// Items have already been loaded
				if (this.activeStudents.length > 0)
				{
					this.isLoading = false
					this.placeholderMsg = "Rut del estudiante"
				}
			},
			lastStudent: function () {
				// is triggered whenever the store state changes
				this.value = this.lastStudent
				this.changeCurrentStudent(this.lastStudent)
				this.changeShow()
			},
			response: function()
			{
				this.changeCurrentStudent(this.response)
			}
		}


}
</script>