<template>
<v-card>

    <!-- Tabla revision escritura -->
    <v-card v-if="this.showTable === true">
        <h4 style="text-align: center; color:white; background-color:#191970">Revisiones generales</h4>
        <v-card-title>
            <v-row style="text-align: center">
                <v-col cols="6" style="text-align: center; justify-content: center; margin-left: 27%">
                    <v-text-field
                            v-model="search"
                            append-icon="search"
                            label="Ingrese el rut del estudiante o corrector que desea buscar"
                            single-line
                            hint="Ingrese el rut con puntos y guión. Ejemplo: 10.000.000-1 o 1.000.000-1" 
                    ></v-text-field>
                </v-col>
                <v-col>
                    <v-icon @click="refreshData" class="ml-5" :color="refreshColor">mdi-refresh-circle</v-icon>
                    <span class="mt-5" style="font-size: 17px"> {{refreshMsg}} </span>
                </v-col>
            </v-row>
        </v-card-title>

        <v-row>
            <v-col cols="6">
                <h5 style="margin-left: 60%; color: gray" >Items</h5>
            </v-col>
            <v-col cols="6">
                <h5 style="margin-left: 38%; color: gray" >Items</h5>
            </v-col>

        </v-row>

        <v-data-table dense
                      :headers="headersEscritura"
                      :items="this.contentEsc"
                      :search="search"
                      :items-per-page="15"
        >
            <template v-slot:item.discrepEsc="{ item }">
                <v-chip style="width: 70%; text-align: center; justify-content: center" :color="getColor(item.discrepEsc)" dark>{{ item.discrepEsc }}</v-chip>
            </template>
            <template v-slot:item.countEscritura="{ item }">
                <v-chip style="width: 70%; text-align: center; justify-content: center" :color="getColorCant(item.countEscritura)" dark>{{ item.countEscritura }}</v-chip>
            </template>
            <template v-slot:item.action="{ item }">
                <v-icon
                        small
                        class="mr-2"
                        @click="editItem(item)"
                >
                    edit
                </v-icon>
            </template>
        </v-data-table>
    </v-card>

    <!-- Ingreso de puntajes de supervision Escritura -->
    <v-dialog v-model="dialog" max-width="600px">
        <v-card>
            <h4 class="info" style="text-align: center; color:white">Tercera revisión</h4>
            <v-card-text>
                <v-container >
                    <h4 style="text-align: center">  Información del alumno: </h4>
                    <v-spacer></v-spacer>
                    <v-row style="text-align: center">
                        <v-col cols="6">
                            Nombre: {{editedItem.name}} {{editedItem.last_name}}
                        </v-col>

                        <v-col cols="6">
                            Rut: {{editedItem.rut}}
                        </v-col>
                    </v-row>

                    <v-row style="text-align: center">
                        <v-col cols="6">
                            <v-btn v-on:click="correctionButtonEsc">Corrección</v-btn>
                        </v-col>
                        <v-col cols="6">
                            <v-btn v-on:click="reCorrectionButtonEsc">Recorrección</v-btn>
                        </v-col>
                    </v-row>


                    <v-row v-if="showCorrectionScoreEsc">

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Estructura</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[0]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Coherencia local</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[1]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Ortografía acentual y literal</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[2]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Ortografía puntual</p>-
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[3]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Léxico</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[4]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Recursos de nivel gramatical</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[5]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Estructura de párrafos</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[6]}}</h3>
                        </v-col>

                        <v-col cols="6">
                            <p style="text-align:center" class="mt-5" >Transformación de conocimientos</p>
                        </v-col>

                        <v-col cols="6">
                            <h3 style="text-align:left" class="mt-5" >{{this.editedItem.supervisorScore[0].scoreObtained[7]}}</h3>
                        </v-col>


                        <v-col cols="12" style="align-content: center; text-align: center">
                            <v-btn v-on:click="back">Atrás</v-btn>
                        </v-col>
                    </v-row>

                    <v-alert type="warning" dismissible v-if="validMessageEsc==true" style="width: 80%; margin-left:10%">
                        Debe rellenar todos los campos
                    </v-alert>


                    <v-row v-if="!showCorrectionScoreEsc">
                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Estructura</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[0]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Coherencia local</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[1]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Ortografía acentual y literal</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[2]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Ortografía puntual</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[3]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Léxico</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[4]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Recursos de nivel gramatical</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[5]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Estructura de párrafos</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[6]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                        <v-row>
                            <v-col cols="4">
                                <p style="text-align:right" class="mt-5" >Transformación de conocimientos</p>
                            </v-col>
                            <v-col cols="5">
                                <v-select :items="codes.writing" :rules="[v => !!v || 'Se requiere de un código']" item-text="code" item-value="code" required
                                          v-model="editedItem.supervisorScore[0].scoreObtained[7]" label="Código"></v-select>
                            </v-col>
                        </v-row>

                    </v-row>
                </v-container>
            </v-card-text>

            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="save">Ingresar notas</v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
    <br>

    <!-- Tercera revision escritura -->
    <v-card>
        <h4 style="text-align: center; color:white; background-color:#191970">Tercera revisión</h4>
        <v-card-title>
            <v-row style="text-align: center">
                <v-col cols="6" style="text-align: center; justify-content: center; margin-left: 27%">
                    <v-text-field style="margin-left: auto ; margin-right: auto"
                                  v-model="search"
                                  append-icon="search"
                                  label="Ingrese el rut del estudiante o corrector que desea buscar"
                                  single-line
                                  hint="Ingrese el rut con puntos y guión. Ejemplo: 10.000.000-1 o 1.000.000-1" 
                    ></v-text-field>
                </v-col></v-row>
        </v-card-title>
        <v-row>
            <v-col cols="12">
                <h5 style="margin-left: 50%; color: gray" >Items</h5>
            </v-col>
        </v-row>
        <v-data-table dense
                      :headers="headersEscrituraSup"
                      :items="this.thirdRevisionEsc"
                      :search="search"
                      :items-per-page="15"
        >
            <template v-slot:item.discrepEsc="{ item }">
                <v-chip style="width: 25%; text-align: center; justify-content: center; margin-left: 10%" :color="getColor(item.discrepEsc)" dark>{{ item.discrepEsc }}</v-chip>
            </template>
            <template v-slot:item.countEscritura="{ item }">
                <v-chip style="width: 25%; text-align: center; justify-content: center; margin-left: 10%" :color="getColorCant(item.countEscritura)" dark>{{ item.countEscritura }}</v-chip>
            </template>
        </v-data-table>
    </v-card>


</v-card  >
</template>

<script>
    import {mapMutations, mapState} from "vuex";
    import axios from 'axios';


    export default {
        data() {
            return {
                showTable: true,
                showCorrectionScoreEsc: false,
                validEsc: false,
                refreshColor: "primary",
                validMessageEsc: false,
                refreshMsg: "Actualizar datos",
                codes:
                    {
                        writing: [
                            {code: '0', def: 'Respuesta correcta'},
                            {code: '1', def: 'Respuesta parcialmente correcta'},
                            {code: '2', def: 'Respuesta incorrecta'},
                            {code: '3', def: 'Respuesta incorrecta'},
                            {code: '4', def: 'Respuesta incorrecta'},
                            {code: '99', def: 'Respuesta omitida'}
                        ],
                    },
                dialog: false,
                search: '',
                headersEscritura: [
                    {text: 'Estudiante', value: 'rut'},
                    {text: 'Corrector', value: 'scores[0].rutCorrector'},
                    {text: 'Fecha', value: 'scores[0].date'},
                    {text: '1', value: 'scores[0].scoreObtained[0]'},
                    {text: '2', value: 'scores[0].scoreObtained[1]'},
                    {text: '3', value: 'scores[0].scoreObtained[2]'},
                    {text: '4', value: 'scores[0].scoreObtained[3]'},
                    {text: '5', value: 'scores[0].scoreObtained[4]'},
                    {text: '6', value: 'scores[0].scoreObtained[5]'},
                    {text: '7', value: 'scores[0].scoreObtained[6]'},
                    {text: '8', value: 'scores[0].scoreObtained[7]'},
                    {text: 'Recorrector', value: 'recorrection[0].rutCorrector'},
                    {text: 'Fecha', value: 'recorrection[0].date'},
                    {text: '1', value: 'recorrection[0].scoreObtained[0]'},
                    {text: '2', value: 'recorrection[0].scoreObtained[1]'},
                    {text: '3', value: 'recorrection[0].scoreObtained[2]'},
                    {text: '4', value: 'recorrection[0].scoreObtained[3]'},
                    {text: '5', value: 'recorrection[0].scoreObtained[4]'},
                    {text: '6', value: 'recorrection[0].scoreObtained[5]'},
                    {text: '7', value: 'recorrection[0].scoreObtained[6]'},
                    {text: '8', value: 'recorrection[0].scoreObtained[7]'},
                    {text: 'Discrep.', value: 'discrepEsc'},
                    {text: 'Cant.', value: 'countEscritura'},
                    {text: '', value: 'action'}
                ],
                headersEscrituraSup: [
                    {text: 'Estudiante', value: 'rut'},
                    {text: 'Corrector', value: 'supervisorScore[0].rutCorrector'},
                    {text: 'Fecha', value: 'supervisorScore[0].date'},
                    {text: '1', value: 'supervisorScore[0].scoreObtained[0]'},
                    {text: '2', value: 'supervisorScore[0].scoreObtained[1]'},
                    {text: '3', value: 'supervisorScore[0].scoreObtained[2]'},
                    {text: '4', value: 'supervisorScore[0].scoreObtained[3]'},
                    {text: '5', value: 'supervisorScore[0].scoreObtained[4]'},
                    {text: '6', value: 'supervisorScore[0].scoreObtained[5]'},
                    {text: '7', value: 'supervisorScore[0].scoreObtained[6]'},
                    {text: '8', value: 'supervisorScore[0].scoreObtained[7]'},
                    {text: 'Discrepancia', value: 'discrepEsc'},
                    {text: 'Cantidad', value: 'countEscritura'}
                ],
                content: null,
                contentEsc: [],
                thirdRevisionEsc: [],
                countEsc: 0,
                editedItem: {
                    rut: '',
                    supervisorScore: [
                        {
                            subject: "",
                            scoreObtained: []
                        },
                        {
                            subject: "",
                            scoreObtained: []
                        }
                    ],
                    discrepEsc: '',
                    countEsc: 0,
                    discrepMat: '',
                    countMat: 0
                },
                defaultItem: {
                    rut: '',
                    supervisorScore: [
                        {
                            scoreObtained: []
                        },
                        {
                            scoreObtained: []
                        }
                    ],
                    discrepEsc: '',
                    countEsc: 0,
                    discrepMat: '',
                    countMat: 0
                },
                editedIndex: -1,
            }
        },


        computed:
            {
                ...mapState(['activeStudents','serverRoute']),
                formTitle() {
                    return this.editedIndex === -1 ? 'New Item' : 'Ingreso de tercera evaluación'
                },
            },

        created: function () {
            this.content = null
            this.content = JSON.parse(JSON.stringify(this.activeStudents));
            this.losOdio();
            // eslint-disable-next-line no-console
            //console.log("informacion de usuario", JSON.parse(sessionStorage.getItem('infoUser')).rut)
        },

        mounted: function () {
    
        },


        methods: {
            ...mapMutations(['getActiveStudents']),
            refreshData()
            {

                if(this.refreshMsg == "Datos actualizados")
                {
                    this.refreshColor = "warning"
                    this.refreshMsg = "Actualizando datos..."
                }

                this.$store.dispatch('getActiveStudents');
                
                this.sleep(6000).then(() => {
                    this.refreshColor = "success"
                    this.refreshMsg = "Datos actualizados"
                })

                this.refreshColor = "warning"
                this.refreshMsg = "Actualizando datos..."
                
            },
            losOdio(){
                this.contentEsc = []
                this.thirdRevisionEsc = []
                for (let i=0; i<this.content.length; i++){
                    if (this.content[i].scores[0].rutCorrector === JSON.parse(sessionStorage.getItem('infoUser')).rut ||
                        this.content[i].recorrection[0].rutCorrector === JSON.parse(sessionStorage.getItem('infoUser')).rut){
                    this.contentEsc.push(this.content[i])
                    }
                    else{
                        // eslint-disable-next-line no-console
                        //console.log("me salto")
                    }
                }
            },

            back() {
                this.showCorrectionScoreEsc = false;
            },

            sleep(time) {
                return new Promise((resolve) => setTimeout(resolve, time));
            },

            getColor(data) {
                if (data === "Si") return 'red';
                else if (data === "-") return 'orange';
                else if (data === "+") return 'blue';
                else return 'green'
            },

            getColorCant(data) {
                if (data > 0) return 'red';
                else return 'green'
            },

            editItem(item) {
                this.editedIndex = this.contentEsc.indexOf(item);
                this.editedItem = Object.assign({}, item);
                this.defaultItem = this.activeStudents[this.editedIndex];
                this.dialog = true
            },
            close() {
                this.dialog = false;
                setTimeout(() => {
                    this.editedItem = this.defaultItem;
                    //this.$store.dispatch('getActiveStudents');
                    this.editedIndex = -1
                }, 300);
                this.showCorrectionScoreEsc = false;
            },

            save() {
                this.validMessageEsc = false;
                this.validEsc = false;

                if (this.editedItem.supervisorScore[0].scoreObtained[0] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[1] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[2] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[3] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[4] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[5] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[6] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[7] !== null){
                    // eslint-disable-next-line no-console
                    console.log("post valido")
                    this.validEsc = true
                }
                else{
                    // eslint-disable-next-line no-console
                    console.log("mssg error")
                    this.validMessageEsc = true
                }


                if (this.editedIndex > -1 ) {
                    Object.assign(this.contentEsc[this.editedIndex], this.editedItem);
                    if(this.validEsc === true) {
                        this.editedItem.supervisorScore[0].rutCorrector = JSON.parse(sessionStorage.getItem('infoUser')).rut;
                        let urlEsc = this.serverRoute + '/supervisorScore/post?StudentRut=' + this.editedItem.rut;
                        axios.post(urlEsc, this.editedItem.supervisorScore[0])
                            .then(response => {
                                this.showTable = false
                                //this.$store.dispatch('getActiveStudents');
                                this.showTable = true
                                // eslint-disable-next-line no-console
                                console.log(response);
                            })
                            .catch(function (error) {
                                // eslint-disable-next-line no-console
                                console.log(error);
                            });
                        this.thirdRevisionEsc = [];
                        this.getThirdRevision();
                        this.calcDiscrepEscritura();
                        this.close()
                    }
                    else{
                        // eslint-disable-next-line no-console
                        //console.log("no funco")
                    }
                }
            },

            getThirdRevision() {
                for (let i = 0; i < this.contentEsc.length; i++) {
                    if (this.contentEsc[i].supervisorScore[0].scoreObtained[0] !== null) {
                        this.thirdRevisionEsc.push(this.contentEsc[i]);
                        this.contentEsc[i].discrepEsc = "+"
                    }
                }
            },

            calcDiscrepEscritura() {
                for (let i = 0; i < this.contentEsc.length; i++) {
                    for (let j = 0; j < 8; j++) {
                        if (this.contentEsc[i].supervisorScore[0].scoreObtained[0] !== null) {
                            this.contentEsc[i].discrepEsc = "+";
                            this.countMat = 0
                        }
                        else if (this.contentEsc[i].scores[0].scoreObtained == null && this.contentEsc[i].recorrection[0].scoreObtained == null) {
                            this.contentEsc[i].discrepEsc = "-"
                        }
                        else if (this.contentEsc[i].scores[0].scoreObtained == null || this.contentEsc[i].recorrection[0].scoreObtained == null) {
                            this.contentEsc[i].discrepEsc = "-"
                        }
                        else if (this.contentEsc[i].scores[0].scoreObtained[j] !== this.contentEsc[i].recorrection[0].scoreObtained[j]) {
                            this.contentEsc[i].discrepEsc = "Si";
                            this.countEsc += 1
                        }
                        else {
                            if (this.countEsc > 0){
                                this.contentEsc[i].discrepEsc = "Si";
                            }
                            else{
                                this.contentEsc[i].discrepEsc = "No"
                            }
                        }
                    }
                    this.contentEsc[i].countEscritura = this.countEsc;
                    this.countEsc = 0
                }
            },

            changeDate(count, subject) {
                if (this.contentEsc[count].supervisorScore[subject].date !== null) {
                    this.contentEsc[count].supervisorScore[subject].date = this.contentEsc[count].supervisorScore[subject].date.split("T")[0]
                }
                if (this.contentEsc[count].scores[subject].date !== null) {
                    this.contentEsc[count].scores[subject].date = this.contentEsc[count].scores[subject].date.split("T")[0]
                }
                if (this.contentEsc[count].recorrection[subject].date !== null) {
                    this.contentEsc[count].recorrection[subject].date = this.contentEsc[count].recorrection[subject].date.split("T")[0]
                }
            },

            correctionButtonEsc() {
                this.editedItem.supervisorScore[0].scoreObtained[0] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[0];
                this.editedItem.supervisorScore[0].scoreObtained[1] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[1];
                this.editedItem.supervisorScore[0].scoreObtained[2] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[2];
                this.editedItem.supervisorScore[0].scoreObtained[3] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[3];
                this.editedItem.supervisorScore[0].scoreObtained[4] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[4];
                this.editedItem.supervisorScore[0].scoreObtained[5] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[5];
                this.editedItem.supervisorScore[0].scoreObtained[6] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[6];
                this.editedItem.supervisorScore[0].scoreObtained[7] = this.contentEsc[this.editedIndex].scores[0].scoreObtained[7];
                this.showCorrectionScoreEsc = false;
                this.showCorrectionScoreEsc = true
            },

            reCorrectionButtonEsc() {
                this.editedItem.supervisorScore[0].scoreObtained[0] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[0];
                this.editedItem.supervisorScore[0].scoreObtained[1] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[1];
                this.editedItem.supervisorScore[0].scoreObtained[2] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[2];
                this.editedItem.supervisorScore[0].scoreObtained[3] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[3];
                this.editedItem.supervisorScore[0].scoreObtained[4] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[4];
                this.editedItem.supervisorScore[0].scoreObtained[5] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[5];
                this.editedItem.supervisorScore[0].scoreObtained[6] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[6];
                this.editedItem.supervisorScore[0].scoreObtained[7] = this.contentEsc[this.editedIndex].recorrection[0].scoreObtained[7];
                this.showCorrectionScoreEsc = false;
                this.showCorrectionScoreEsc = true
            },

        },

        watch: {
            activeStudents: function () {
                this.content = null
                this.content = JSON.parse(JSON.stringify(this.activeStudents))
                this.losOdio();
            },
            content: function () {
                //this.losOdio();
                this.calcDiscrepEscritura();
                this.getThirdRevision()
            },

            dialog(val) {
                val || this.close()
            }
        }
    }

</script>

