<script>
    import {mapState, mapMutations} from 'vuex';
    import axios from 'axios';
    import ItemTable from "./ItemTable";
    import CorrectorTable from "./CorrectorTable";
    export default {
        components:{
            ItemTable,
            CorrectorTable
        },
        data () {
            return {
                validEsc: false,
                validMessageEsc: false,
                validMat: false,
                validMessageMat: false,
                showCorrectionScoreMat: false,
                showCorrectionScoreEsc: false,
                supervisionLabel: "Seleccione lo que desea supervisar",
                disableSupervision: false,
                refreshMsg: "Actualizar datos",
                refreshColor: "primary",
                codes:
                    {
                        math:[
                            {code:'20',def:'Respuesta correcta'},
                            {code:'10',def:'Respuesta parcialmente correcta'},
                            {code:'70',def:'Respuesta incorrecta'},
                            {code:'99',def:'Respuesta omitida'}
                        ],
                        writing:[
                            {code:'0',def:'Respuesta correcta'},
                            {code:'1',def:'Respuesta parcialmente correcta'},
                            {code:'2',def:'Respuesta incorrecta'},
                            {code:'3',def:'Respuesta incorrecta'},
                            {code:'4',def:'Respuesta incorrecta'},
                            {code:'99',def:'Respuesta omitida'}
                        ] ,
                    },
                dialog: false,
                items: ["Escritura", "Matemática"],
                selection: null,
                search: '',
                search2: '',
                headersEscritura: [
                    { text: 'Estudiante', value: 'rut'},
                    { text: 'Corrector', value: 'scores[0].rutCorrector'},
                    { text: 'Fecha', value: 'scores[0].date'},
                    { text: '1',  value: 'scores[0].scoreObtained[0]'},
                    { text: '2',  value: 'scores[0].scoreObtained[1]'},
                    { text: '3',  value: 'scores[0].scoreObtained[2]'},
                    { text: '4',  value: 'scores[0].scoreObtained[3]'},
                    { text: '5',  value: 'scores[0].scoreObtained[4]'},
                    { text: '6',  value: 'scores[0].scoreObtained[5]'},
                    { text: '7',  value: 'scores[0].scoreObtained[6]'},
                    { text: '8',  value: 'scores[0].scoreObtained[7]'},
                    { text: 'Recorrector', value: 'recorrection[0].rutCorrector'},
                    { text: 'Fecha', value: 'recorrection[0].date'},
                    { text: '1',  value: 'recorrection[0].scoreObtained[0]'},
                    { text: '2',  value: 'recorrection[0].scoreObtained[1]'},
                    { text: '3',  value: 'recorrection[0].scoreObtained[2]'},
                    { text: '4',  value: 'recorrection[0].scoreObtained[3]'},
                    { text: '5',  value: 'recorrection[0].scoreObtained[4]'},
                    { text: '6',  value: 'recorrection[0].scoreObtained[5]'},
                    { text: '7',  value: 'recorrection[0].scoreObtained[6]'},
                    { text: '8',  value: 'recorrection[0].scoreObtained[7]'},
                    { text: 'Discrep.', value: 'discrepEsc'},
                    { text: 'Cant.', value: 'countEscritura'},
                    { text: '', value: 'action'}
                ],
                headersMatematica: [
                    { text: 'Estudiante', value: 'rut'},
                    { text: 'Corrector', value: 'scores[1].rutCorrector'},
                    { text: 'Fecha', value: 'scores[1].date'},
                    { text: 'Pregunta 27',  value: 'scores[1].scoreObtained[0]'},
                    { text: 'Pregunta 28',  value: 'scores[1].scoreObtained[1]'},
                    { text: 'Pregunta 29',  value: 'scores[1].scoreObtained[2]'},
                    { text: 'Recorrector', value: 'recorrection[1].rutCorrector'},
                    { text: 'Fecha', value: 'recorrection[1].date'},
                    { text: 'Pregunta 27',  value: 'recorrection[1].scoreObtained[0]'},
                    { text: 'Pregunta 28',  value: 'recorrection[1].scoreObtained[1]'},
                    { text: 'Pregunta 29',  value: 'recorrection[1].scoreObtained[2]'},
                    { text: 'Discrepancia', value: 'discrepMat'},
                    { text: 'Cantidad', value: 'countMatematica'},
                    { text: '', value: 'action'}
                    ],
                headersEscrituraSup: [
                    { text: 'Estudiante', value: 'rut'},
                    { text: 'Supervisor', value: 'supervisorScore[0].rutCorrector'},
                    { text: 'Fecha', value: 'supervisorScore[0].date'},
                    { text: '1',  value: 'supervisorScore[0].scoreObtained[0]'},
                    { text: '2',  value: 'supervisorScore[0].scoreObtained[1]'},
                    { text: '3',  value: 'supervisorScore[0].scoreObtained[2]'},
                    { text: '4',  value: 'supervisorScore[0].scoreObtained[3]'},
                    { text: '5',  value: 'supervisorScore[0].scoreObtained[4]'},
                    { text: '6',  value: 'supervisorScore[0].scoreObtained[5]'},
                    { text: '7',  value: 'supervisorScore[0].scoreObtained[6]'},
                    { text: '8',  value: 'supervisorScore[0].scoreObtained[7]'},
                    { text: 'Discrepancia', value: 'discrepEsc'},
                    { text: 'Cantidad', value: 'countEscritura'}
                ],
                headersMatematicaSup: [
                    { text: 'Estudiante', value: 'rut'},
                    { text: 'Supervisor', value: 'supervisorScore[1].rutCorrector'},
                    { text: 'Fecha', value: 'supervisorScore[1].date'},
                    { text: 'Pregunta 27',  value: 'supervisorScore[1].scoreObtained[0]'},
                    { text: 'Pregunta 28',  value: 'supervisorScore[1].scoreObtained[1]'},
                    { text: 'Pregunta 29',  value: 'supervisorScore[1].scoreObtained[2]'},
                    { text: 'Discrepancia', value: 'discrepMat'},
                    { text: 'Cantidad', value: 'countMatematica'}
                ],
                content: null,
                thirdRevisionEsc: [],
                thirdRevisionMat: [],
                countEsc: 0,
                countMat: 0,
                editedItem: {
                    rut: '',
                    supervisorScore: [
                        {
                            subject: "",
                            scoreObtained: []
                        },
                        {
                            subject: "",
                            scoreObtained: []
                        }
                    ],
                   discrepEsc: '',
                    countEsc: 0,
                    discrepMat: '',
                    countMat: 0
                },
                defaultItem: {
                    rut: '',
                    supervisorScore: [
                        {
                            scoreObtained: []
                        },
                        {
                            scoreObtained: []
                        }
                    ],
                    discrepEsc: '',
                    countEsc: 0,
                    discrepMat: '',
                    countMat: 0
                },
                editedIndex: -1,
            }
        },

        computed:
            {
                ...mapState(['activeStudents','showTable', 'tableSelection', 'showItemTable','serverRoute']),
                formTitle () {
                    return this.editedIndex === -1 ? 'New Item' : 'Ingreso de tercera evaluación'
                },
            },

        created:function(){
            this.$store.dispatch('getCorrectorsTableWriting')
            this.$store.dispatch('getCorrectorsTableMath')

            if(this.activeStudents.length == 0)
            {
                this.supervisionLabel = "Cargando datos..."
                this.disableSupervision = true
            }

            // eslint-disable-next-line no-console
            //console.log("informacion de usuario", JSON.parse(sessionStorage.getItem('infoUser')).rut)
        },

        mounted:function(){

        },


        methods:{
            ...mapMutations(['changeTableSelection', 'changeViewItemTable']),
            refreshData()
            {

                if(this.refreshMsg == "Datos actualizados")
                {
                    this.refreshColor = "warning"
                    this.refreshMsg = "Actualizando datos..."
                }

                this.$store.dispatch('getActiveStudents');
                
                this.sleep(6000).then(() => {
                    this.refreshColor = "success"
                    this.refreshMsg = "Datos actualizados"
                })

                this.refreshColor = "warning"
                this.refreshMsg = "Actualizando datos..."
                
            },
            back(){
              this.showCorrectionScoreEsc = false;
              this.showCorrectionScoreMat = false
            },

            sleep (time) {
                return new Promise((resolve) => setTimeout(resolve, time));
            },

            getColor (data) {
                if (data === "Si") return 'red';
                else if (data === "-") return 'orange';
                else if (data === "+") return 'blue';
                else return 'green'
            },

            getColorCant (data) {
                if (data > 0) return 'red';
                else return 'green'
            },

            editItem (item) {
                this.editedIndex = this.content.indexOf(item);
                // eslint-disable-next-line no-console
                this.editedItem = Object.assign({}, item);
                //this.editedIndex = this.content[this.editedIndex]
                this.defaultItem = this.activeStudents[this.editedIndex];
                this.dialog = true
            },
            close () {
                this.dialog = false;
                setTimeout(() => {
                    this.editedItem = this.defaultItem;
                    //this.$store.dispatch('getAllStudents');

                    this.editedIndex = -1
                }, 300);
                this.showCorrectionScoreMat= false;
                this.showCorrectionScoreEsc= false;
            },

            save () {
                this.validMessageMat = false;
                this.validMat = false;
                this.validMessageEsc = false;
                this.validEsc = false;

                if (this.editedItem.supervisorScore[1].scoreObtained[0] !== null &&
                    this.editedItem.supervisorScore[1].scoreObtained[1] !== null &&
                    this.editedItem.supervisorScore[1].scoreObtained[2] !== null &&
                    this.selection === "Matemática"){
                            this.validMat = true
                        // eslint-disable-next-line no-console
                        //console.log("post valido")
                }
                else{
                    // eslint-disable-next-line no-console
                    //console.log("mssg error")
                    this.validMessageMat = true
                }

                if (this.editedItem.supervisorScore[0].scoreObtained[0] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[1] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[2] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[3] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[4] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[5] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[6] !== null &&
                    this.editedItem.supervisorScore[0].scoreObtained[7] !== null &&
                    this.selection === "Escritura"){
                        // eslint-disable-next-line no-console
                        //console.log("post valido")
                        this.validEsc = true
                }
                else{
                    // eslint-disable-next-line no-console
                    //console.log("mssg error")
                    this.validMessageEsc = true
                }

                if (this.editedIndex > -1 ) {
                    Object.assign(this.content[this.editedIndex], this.editedItem);
                    if (this.selection === "Matemática" && this.validMat === true) {
                        this.editedItem.supervisorScore[1].rutCorrector = JSON.parse(sessionStorage.getItem('infoUser')).rut
                        let urlMat = this.serverRoute + '/supervisorScore/post?StudentRut=' + this.editedItem.rut;
                        axios.post(urlMat, this.editedItem.supervisorScore[1])
                            .then(response => {
                                this.$store.dispatch('getActiveStudents');
                                // eslint-disable-next-line no-console
                                console.log(response);
                            })
                            .catch(function (error) {
                                // eslint-disable-next-line no-console
                                console.log(error);
                            });
                        this.close()


                    }
                    else if (this.selection === "Escritura" && this.validEsc === true) {
                        this.editedItem.supervisorScore[0].rutCorrector = JSON.parse(sessionStorage.getItem('infoUser')).rut
                        let urlEsc = this.serverRoute + '/supervisorScore/post?StudentRut=' + this.editedItem.rut;
                        axios.post(urlEsc, this.editedItem.supervisorScore[0])
                            .then(response => {
                                this.$store.dispatch('getActiveStudents');
                                // eslint-disable-next-line no-console
                                console.log(response);
                            })
                            .catch(function (error) {
                                // eslint-disable-next-line no-console
                                console.log(error);
                            });
                        this.close()

                    }
                else {
                        // eslint-disable-next-line no-console
                        //console.log("no funco")
                    }
                }
                this.thirdRevisionMat = [];
                this.thirdRevisionEsc = [];
                this.getThirdRevision();
            },

            getThirdRevision(){
                for (let i=0; i<this.content.length; i++){
                    if (this.content[i].supervisorScore[0].scoreObtained[0] !== null){
                        this.thirdRevisionEsc.push(this.content[i]);
                        this.content[i].discrepEsc = "+"
                    }
                    if (this.content[i].supervisorScore[1].scoreObtained[0] !== null){
                        this.thirdRevisionMat.push(this.content[i]);
                        this.content[i].discrepMat = "+"
                    }
                }
            },

            calcDiscrepEscritura() {
                if (this.content.length > 0) {
                for (let i = 0; i < this.content.length; i++) {
                    for (let j = 0; j < 8; j++) {
                        if (this.content[i].supervisorScore[0].scoreObtained[0] !== null) {
                            this.content[i].discrepEsc = "+";
                            this.countMat = 0
                        } else if (this.content[i].scores[0].scoreObtained == null && this.content[i].recorrection[0].scoreObtained == null) {
                            this.content[i].discrepEsc = "-"
                        } else if (this.content[i].scores[0].scoreObtained == null || this.content[i].recorrection[0].scoreObtained == null) {
                            this.content[i].discrepEsc = "-"
                        } else if (this.content[i].scores[0].scoreObtained[j] !== this.content[i].recorrection[0].scoreObtained[j]) {
                            this.content[i].discrepEsc = "Si";
                            this.countEsc += 1
                        } else {
                            if (this.countEsc > 0) {
                                this.content[i].discrepEsc = "Si";
                            } else {
                                this.content[i].discrepEsc = "No"
                            }
                        }
                    }
                    //this.changeDate(i, 0);
                    this.content[i].countEscritura = this.countEsc;
                    this.countEsc = 0
                }
            }
            },

            calcDiscrepMatematica(){
                if (this.content.length > 0) {
                for(let i=0; i<this.content.length; i++) {
                    for (let j = 0; j < 3; j++) {
                        if (this.content[i].supervisorScore[1].scoreObtained[0] !== null) {
                            this.content[i].discrepMat = "+";
                            this.countMat = 0
                        } else if (this.content[i].scores[1].scoreObtained == null && this.content[i].recorrection[1].scoreObtained == null) {
                            this.content[i].discrepMat = "-"
                        } else if (this.content[i].scores[1].scoreObtained == null || this.content[i].recorrection[1].scoreObtained == null) {
                            this.content[i].discrepMat = "-"
                        } else if (this.content[i].scores[1].scoreObtained[j] !== this.content[i].recorrection[1].scoreObtained[j]) {
                            this.content[i].discrepMat = "Si";
                            this.countMat += 1
                        } else {
                            if (this.countMat > 0) {
                                // eslint-disable-next-line no-console
                                //console.log("entre", this.countMat, "if")
                                this.content[i].discrepMat = "Si";
                            } else {
                                // eslint-disable-next-line no-console
                                //console.log("entre else", this.countMat)
                                this.content[i].discrepMat = "No"
                            }

                        }
                    }
                    //this.changeDate(i, 1);
                    this.content[i].countMatematica = this.countMat;
                    this.countMat = 0

                }
                }
            },

            changeDate(count, subject){
                if(this.content[count].supervisorScore[subject].date !== null){
                    this.content[count].supervisorScore[subject].date = this.content[count].supervisorScore[subject].date.split("T")[0]
                }
                if(this.content[count].scores[subject].date !== null) {
                    this.content[count].scores[subject].date = this.content[count].scores[subject].date.split("T")[0]
                }
                if(this.content[count].recorrection[subject].date !== null) {
                    this.content[count].recorrection[subject].date = this.content[count].recorrection[subject].date.split("T")[0]
                }
            },

            correctionButtonMat(){
                this.editedItem.supervisorScore[1].scoreObtained[0] = this.content[this.editedIndex].scores[1].scoreObtained[0];
                this.editedItem.supervisorScore[1].scoreObtained[1] = this.content[this.editedIndex].scores[1].scoreObtained[1];
                this.editedItem.supervisorScore[1].scoreObtained[2] = this.content[this.editedIndex].scores[1].scoreObtained[2];
                this.showCorrectionScoreMat = false;
                this.showCorrectionScoreMat = true
               },

            reCorrectionButtonMat(){
                this.editedItem.supervisorScore[1].scoreObtained[0] = this.content[this.editedIndex].recorrection[1].scoreObtained[0];
                this.editedItem.supervisorScore[1].scoreObtained[1] = this.content[this.editedIndex].recorrection[1].scoreObtained[1];
                this.editedItem.supervisorScore[1].scoreObtained[2] = this.content[this.editedIndex].recorrection[1].scoreObtained[2];
                this.showCorrectionScoreMat = false;
                this.showCorrectionScoreMat = true
            },

            correctionButtonEsc(){
                this.editedItem.supervisorScore[0].scoreObtained[0] = this.content[this.editedIndex].scores[0].scoreObtained[0];
                this.editedItem.supervisorScore[0].scoreObtained[1] = this.content[this.editedIndex].scores[0].scoreObtained[1];
                this.editedItem.supervisorScore[0].scoreObtained[2] = this.content[this.editedIndex].scores[0].scoreObtained[2];
                this.editedItem.supervisorScore[0].scoreObtained[3] = this.content[this.editedIndex].scores[0].scoreObtained[3];
                this.editedItem.supervisorScore[0].scoreObtained[4] = this.content[this.editedIndex].scores[0].scoreObtained[4];
                this.editedItem.supervisorScore[0].scoreObtained[5] = this.content[this.editedIndex].scores[0].scoreObtained[5];
                this.editedItem.supervisorScore[0].scoreObtained[6] = this.content[this.editedIndex].scores[0].scoreObtained[6];
                this.editedItem.supervisorScore[0].scoreObtained[7] = this.content[this.editedIndex].scores[0].scoreObtained[7];
                this.showCorrectionScoreEsc = false;
                this.showCorrectionScoreEsc = true
                },

            reCorrectionButtonEsc(){
                this.editedItem.supervisorScore[0].scoreObtained[0] = this.content[this.editedIndex].recorrection[0].scoreObtained[0];
                this.editedItem.supervisorScore[0].scoreObtained[1] = this.content[this.editedIndex].recorrection[0].scoreObtained[1];
                this.editedItem.supervisorScore[0].scoreObtained[2] = this.content[this.editedIndex].recorrection[0].scoreObtained[2];
                this.editedItem.supervisorScore[0].scoreObtained[3] = this.content[this.editedIndex].recorrection[0].scoreObtained[3];
                this.editedItem.supervisorScore[0].scoreObtained[4] = this.content[this.editedIndex].recorrection[0].scoreObtained[4];
                this.editedItem.supervisorScore[0].scoreObtained[5] = this.content[this.editedIndex].recorrection[0].scoreObtained[5];
                this.editedItem.supervisorScore[0].scoreObtained[6] = this.content[this.editedIndex].recorrection[0].scoreObtained[6];
                this.editedItem.supervisorScore[0].scoreObtained[7] = this.content[this.editedIndex].recorrection[0].scoreObtained[7];
                this.showCorrectionScoreEsc = false;
                this.showCorrectionScoreEsc = true
            },

        },

        watch: {
            activeStudents: function () {
                this.content = JSON.parse(JSON.stringify(this.activeStudents))

                this.disableSupervision = false
                this.supervisionLabel = "Seleccione lo que desea Supervisar"
                
                this.thirdRevisionEsc = []
                this.thirdRevisionMat = []
                // eslint-disable-next-line no-console
                //console.log("hola", this.content)
            },

            content: function (){
              this.calcDiscrepEscritura();
              this.calcDiscrepMatematica();
              this.getThirdRevision()
            },

            dialog (val) {
                val || this.close()
            },

            selection: function () {
                this.changeTableSelection(this.selection)
            }

        }
    }
</script>

