<template>
    <v-row>
        <v-col cols="8">
			<div v-if="correctionType == 1">
				<v-alert type="success" dismissible v-model="success" style="width: 80%; margin-left:10%">
					{{this.successMessage}}
				</v-alert>

				<v-card	class="mx-auto"
				style="width: 25%; position:fixed; right:7.5%; top:60%" v-if="showEvaluation">
					<v-list-item three-line>
						<v-list-item-content>
							<div class="overline mb-4">Glosario de códigos</div>
							<v-list-item-title class="headline mb-1">Matemática</v-list-item-title>
							<v-list-item-subtitle class="mt-2">
							{{codes.math[0].code}} = {{codes.math[0].def}}
							<br>
							{{codes.math[1].code}} = {{codes.math[1].def}}
							</v-list-item-subtitle>
							<v-list-item-subtitle>
							{{codes.math[2].code}} = {{codes.math[2].def}}
							<br>
							{{codes.math[3].code}} = {{codes.math[3].def}}
							</v-list-item-subtitle>
						</v-list-item-content>
							<v-list-item-avatar
									tile
									size="80"
							>
								<v-icon x-large color="blue" style="float: right">mdi-information-outline</v-icon>
							</v-list-item-avatar>
					</v-list-item>
				</v-card>

				<v-card class="mx-auto" style="width: 80%; height: 100%;" v-if="showEvaluation" >

				<h4 v-if="editMode == 0" class="success" style="text-align: center; color:white">Primera revisión</h4>
				<h4 v-if="editMode == 1 || editMode == 3" class="warning" style="text-align: center; color:white ">Editar puntajes ya ingresados</h4>
				<h4 v-if="editMode == 2" class="info" style="text-align: center; color:white">Segunda revisión</h4>
				<h4 v-if="editMode == 4" class="error" style="text-align: center; color:white">Alumno revisado totalmente</h4>

				<v-row>
					<v-col >	
					<h3 style="text-align: center">Evaluación de desarrollo en Matemática</h3>
					</v-col>	
				</v-row>

				<v-form
					ref="form"
					v-model="valid"
					lazy-validation
					class="ml-5 mb-5 mt-5 mr-5">
				
				<v-row>
					<v-col cols="4">
						<p style="text-align:right" class="mt-5" >Puntaje pregunta 27:</p>
					</v-col>
					
					<v-col cols="5">
						<v-select
							v-model="select1"
							:items="codes.math"
							item-text="code"
							item-value="code"
							:rules="[v => !!v || 'Se requiere de un código']"
							label="Código"
							required
							:disabled= disableValue							
						></v-select>
					</v-col >
				</v-row>
				<v-row>
					<v-col cols="4">
						<p style="text-align:right" class="mt-5" >Puntaje pregunta 28:</p>
					</v-col>
					<v-col cols="5">
						<v-select
							v-model="select2"
							:items="codes.math"
							item-text="code"
							item-value="code"
							:rules="[v => !!v || 'Se requiere de un código']"
							label="Código"
							required
							:disabled= disableValue

						></v-select>
					</v-col >
				</v-row>

				<v-row>
					<v-col cols="4">
					<p style="text-align:right" class="mt-5" >Puntaje pregunta 29:</p>
					</v-col>
					<v-col cols="5">
					<v-select
						v-model="select3"
						:items="codes.math"
						:rules="[v => !!v || 'Se requiere de un código']"
						item-text="code"
						item-value="code"
						label="Código"
						required							
						:disabled= disableValue

						
					></v-select>
					</v-col >
				</v-row>

				<v-card-actions style="justify-content: center" v-if="!disableValue">
					<v-btn 
					:disabled="!valid"
					color="blue"
					text
					class="mr-4 mb-4"
					@click="validate"
					>
					Ingresar notas
					</v-btn>
				</v-card-actions>

				</v-form>
				</v-card>
			</div>

			<div v-if="correctionType == 0">
				
				<v-alert type="success" dismissible v-model="success" style="width: 80%; margin-left:10%">
					{{this.successMessage}}
				</v-alert>

				<v-card	class="mx-auto"
				style="width: 25%; position:fixed; right:7.5%; top:60%" v-if="showEvaluation">
					<v-list-item three-line>
						<v-list-item-content>
							<div class="overline mb-4">Glosario de códigos</div>
							<v-list-item-title class="headline mb-1">Escritura</v-list-item-title>
							<v-list-item-subtitle class="mt-2">
							{{codes.writing[0].code}} = {{codes.writing[0].def}}
							<br>
							{{codes.writing[1].code}} = {{codes.writing[1].def}}
							</v-list-item-subtitle>
							<v-list-item-subtitle>
							{{codes.writing[2].code}} = {{codes.writing[2].def}}
							<br>
							{{codes.writing[3].code}} = {{codes.writing[3].def}}
							</v-list-item-subtitle>
													<v-list-item-subtitle>
							{{codes.writing[4].code}} = {{codes.writing[4].def}}
							<br>
							{{codes.writing[5].code}} = {{codes.writing[5].def}}
							</v-list-item-subtitle>
						</v-list-item-content>
							<v-list-item-avatar
									tile
									size="80"
							>
								<v-icon x-large color="blue" style="float: right">mdi-information-outline</v-icon>
							</v-list-item-avatar>
					</v-list-item>
				</v-card>

				<v-card class="mx-auto" style="width: 80%; height: 100%;" v-if="showEvaluation">


				<h4 v-if="editMode == 0" class="success" style="text-align: center; color:white">Primera revisión</h4>
				<h4 v-if="editMode == 1 || editMode == 3" class="warning" style="text-align: center; color:white ">Editar puntajes ya ingresados</h4>
				<h4 v-if="editMode == 2" class="info" style="text-align: center; color:white">Segunda revisión</h4>
				<h4 v-if="editMode == 4" class="error" style="text-align: center; color:white">Alumno revisado totalmente</h4>


				<v-row>
					<v-col >
					<h3 style="text-align: center">Evaluación de desarrollo en Escritura</h3>
					</v-col>	
				</v-row>

	

				<v-form
					ref="form"
					v-model="valid"
					lazy-validation
					class="ml-5 mb-5 mr-5">
				
				<v-row>
					<v-col cols="4">
						<p style="text-align:right" class="mt-5" >Estructura</p>
					</v-col>
					
					<v-col cols="5">
						<v-select
							v-model="select_a"
							eager
							:items="codes.writing"
							item-text="code"
							item-value="code"
							:rules="[v => !!v || 'Se requiere de un código']"
							label="Código"
							required
							:disabled= disableValue
							
						></v-select>
					</v-col >

				</v-row>
				<v-row>
					<v-col cols="4">
						<p style="text-align:right" class="mt-5" >Coherencia local</p>
					</v-col>
					<v-col cols="5">
						<v-select
							v-model="select_b"
							:items="codes.writing"
							item-text="code"
							item-value="code"
							:rules="[v => !!v || 'Se requiere de un código']"
							label="Código"
							required
							:disabled= disableValue
							
						></v-select>
					</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Ortografía acentual y literal</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model="select_c"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Ortografía puntual</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model ="select_d"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Léxico</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model="select_e"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Recursos de nivel gramatical</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model="select_f"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Estructura de párrafos</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model="select_g"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>

			<v-row>
				<v-col cols="4">
				<p style="text-align:right" class="mt-5" >Transformación de conocimientos</p>
				</v-col>
				<v-col cols="5">
				<v-select
					v-model="select_h"
					:items="codes.writing"
					item-text="code"
					item-value="code"
					:rules="[v => !!v || 'Se requiere de un código']"
					label="Código"
					require							
					:disabled= disableValue
d
					
				></v-select>
				</v-col >
			</v-row>


			<v-card-actions style="justify-content: center" v-if="!disableValue">
				<v-btn 
				:disabled="!valid"
				color="blue"
				text
				class="mr-4 mb-4"
				@click="validate"
				>
				Ingresar notas
				</v-btn>
			</v-card-actions>

			</v-form>
			</v-card>
          </div>
        </v-col>
    </v-row>
</template>

<script>
  import {mapMutations,mapState} from 'vuex';
  import axios from 'axios'

  export default {
    data: () => ({
		valid: true,
		success: false,
		disableValue: false,
		successMessage: "Las notas del estudiante han sido ingresadas correctamente",
		score: 
		{
		rutCorrector: "",
		subject: "",
		scoreObtained: [],
		status: []
		},
		select1: '',
		select2: '',
		select3: '',
		select_a: '',
		select_b: '',
		select_c: '',
		select_d: '',
		select_e: '',
		select_f: '',
		select_g: '',
		select_h: '',
		editMode: '', //0: primera revisión, 1: modo edición del mismo profesor, 2: segunda revisión, 3: segunda revisión edit
		codes:
		{
			math:[
				{code:'20',def:'Respuesta correcta'},
				{code:'10',def:'Respuesta parcialmente correcta'},
				{code:'70',def:'Respuesta incorrecta'},
				{code:'99',def:'Respuesta omitida'}
			],
			writing:[
				{code:'4',def:'Respuesta correcta'},
				{code:'3',def:'Respuesta parcialmente correcta'},
				{code:'2',def:'Respuesta incorrecta'},
				{code:'1',def:'Respuesta incorrecta'},
				{code:'0',def:'Respuesta incorrecta'},
				{code:'99',def:'Respuesta omitida'}
			] ,
		},
		correctionType: null,
		usrInfoLogIn: null
	}),
	computed:
	{
		...mapState(['currentStudent','showEvaluation','usrInfoLogIn','serverRoute']),
	},
	created: function()
	{
		if (JSON.parse(sessionStorage.getItem('infoUser')) == null)
		{
			// eslint-disable-next-line no-console
			//console.log("correnasdads");
		}

		else
		{
			this.correctionType = JSON.parse(sessionStorage.getItem('infoUser')).correctionRol
			this.usrInfoLogIn = JSON.parse(sessionStorage.getItem('infoUser'))
		}
	},
    methods: {
		...mapMutations(['showEvaluationFunct']),
		validate () {
			if (this.$refs.form.validate()) {
				this.snackbar = true
				this.score.rutCorrector = this.usrInfoLogIn.rut

				//Si el rol de corrección es Escritura, entonces scoreObtained guarda los items para la única pregunta.
				if (this.correctionType == 0) 
				{
					this.score.subject = "Escritura"
					this.score.scoreObtained = [this.select_a, this.select_b, this.select_c,  
												this.select_d, this.select_e, this.select_f, 
												this.select_g, this.select_h]
					this.score.status = [0,0,0,0,0,0,0,0]
				}

				else
				{
					this.score.subject = "Matematica"
					this.score.scoreObtained = [this.select1, this.select2, this.select3]
					this.score.status = [0,0,0]
				}

				//En caso de que sea una primera revisión o es una edición, se envía con este axios
				if (this.editMode == 0 || this.editMode == 1) {
					let url = this.serverRoute + '/score/post?StudentRut=' + this.currentStudent.rut
					axios.post(url, this.score)
					.then(response => { 
						// eslint-disable-next-line no-console
						console.log(response);
						this.success = true
						this.showEvaluationFunct(false)
					})
					.catch(function (error) {
						// eslint-disable-next-line no-console
						console.log(error);
					});
				}

				else 
				{
					let url = this.serverRoute + '/recorrection/post?StudentRut=' + this.currentStudent.rut
					axios.post(url, this.score)
					.then(response => { 
						// eslint-disable-next-line no-console
						console.log(response);
						this.success = true
						this.showEvaluationFunct(false)
					})
					.catch(function (error) {
						// eslint-disable-next-line no-console
						console.log(error);
					});
				}

				this.$store.dispatch('getActiveStudents')

			}

		},
	},
	watch:
	{
		success: function()
		{

		},
		currentStudent: function()
		{
			// eslint-disable-next-line no-console
			//console.log("hola",this.currentStudent);

			//Se setea todo en vacío
			this.select1 = ''
			this.select2 = ''
			this.select3 = ''
			this.select_a = ''
			this.select_b = ''
			this.select_c = ''
			this.select_d = ''
			this.select_e = ''
			this.select_f = ''
			this.select_g = ''
			this.select_h = ''
			this.success = false
			this.editMode = 0
			this.disableValue = false;

			//Se comprueba que el estudiante tenga el objeto score de escritura y math
			if (this.currentStudent.scores.length == 2) {

				//Si el corrector es de escritura
				if (this.correctionType == 0) 
				{
					//Si el estudiante tiene score en escritura, entonces se setean a los select
					if (this.currentStudent.scores[0].scoreObtained != null){

						//Si quien revisó el score de el estudiante es el mismo evaluador que está logueado, se deja en modo edición
						if (this.currentStudent.scores[0].rutCorrector == this.usrInfoLogIn.rut) 
						{
							this.editMode = 1	
						}

						//En caso contrario, quiere decir que es una segunda revisión
						else
						{
							this.editMode = 2
						}

						this.select_a = this.select_a.replace('', this.currentStudent.scores[0].scoreObtained[0])
						this.select_b = this.select_b.replace('', this.currentStudent.scores[0].scoreObtained[1])
						this.select_c = this.select_c.replace('', this.currentStudent.scores[0].scoreObtained[2])
						this.select_d = this.select_d.replace('', this.currentStudent.scores[0].scoreObtained[3])
						this.select_e = this.select_e.replace('', this.currentStudent.scores[0].scoreObtained[4])
						this.select_f = this.select_f.replace('', this.currentStudent.scores[0].scoreObtained[5])
						this.select_g = this.select_g.replace('', this.currentStudent.scores[0].scoreObtained[6])
						this.select_h = this.select_h.replace('', this.currentStudent.scores[0].scoreObtained[7])
					}
				}
				//Si el corrector es de math
				else
				{
					//Si el estudiante tiene score en math, entonces se setean a los select
					if (this.currentStudent.scores[1].scoreObtained != null){

						//Si quien revisó el score de el estudiante es el mismo evaluador que está logueado, se deja en modo edición
						if (this.currentStudent.scores[1].rutCorrector == this.usrInfoLogIn.rut) 
						{
							this.editMode = 1	
						}

						//En caso contrario, quiere decir que es una segunda revisión
						else
						{
							this.editMode = 2
						}

						this.select1 = this.select1.replace('',this.currentStudent.scores[1].scoreObtained[0])
						this.select2 = this.select2.replace('',this.currentStudent.scores[1].scoreObtained[1])
						this.select3 = this.select3.replace('',this.currentStudent.scores[1].scoreObtained[2])
					}
				}

				//Existe el caso de que 
				if (this.editMode == 2) 
				{
					//Se setea todo en vacío
					this.select1 = ''
					this.select2 = ''
					this.select3 = ''
					this.select_a = ''
					this.select_b = ''
					this.select_c = ''
					this.select_d = ''
					this.select_e = ''
					this.select_f = ''
					this.select_g = ''
					this.select_h = ''
					this.success = false

					//Se comprueba que el estudiante tenga el objeto recorrection de escritura y math
					if (this.currentStudent.recorrection.length  == 2) {
						//Si el corrector es de escritura
						if (this.correctionType == 0) 
						{
							//Si el estudiante tiene score en escritura, entonces se setean a los select
							if (this.currentStudent.recorrection[0].scoreObtained != null){

								//Si quien revisó el score de el estudiante es el mismo evaluador que está logueado, se deja en modo edición
								if (this.currentStudent.recorrection[0].rutCorrector == this.usrInfoLogIn.rut) 
								{
									this.editMode = 3	
								}

								//En caso contrario, quiere decir que es una segunda revisión
								else
								{
									this.editMode = 4 //Ya están todos los puntajes listos
									this.disableValue = true;
								}

								this.select_a = this.select_a.replace('', this.currentStudent.recorrection[0].scoreObtained[0])
								this.select_b = this.select_b.replace('', this.currentStudent.recorrection[0].scoreObtained[1])
								this.select_c = this.select_c.replace('', this.currentStudent.recorrection[0].scoreObtained[2])
								this.select_d = this.select_d.replace('', this.currentStudent.recorrection[0].scoreObtained[3])
								this.select_e = this.select_e.replace('', this.currentStudent.recorrection[0].scoreObtained[4])
								this.select_f = this.select_f.replace('', this.currentStudent.recorrection[0].scoreObtained[5])
								this.select_g = this.select_g.replace('', this.currentStudent.recorrection[0].scoreObtained[6])
								this.select_h = this.select_h.replace('', this.currentStudent.recorrection[0].scoreObtained[7])
							}
						}
						//Si el corrector es de math
						else
						{
							//Si el estudiante tiene score en math, entonces se setean a los select
							if (this.currentStudent.recorrection[1].scoreObtained != null){

								//Si quien revisó el score de el estudiante es el mismo evaluador que está logueado, se deja en modo edición
								if (this.currentStudent.recorrection[1].rutCorrector == this.usrInfoLogIn.rut) 
								{
									this.editMode = 3	
								}

								//En caso contrario, quiere decir que es una segunda revisión
								else
								{
									this.editMode = 4 //Ya están todos los puntajes listos
									this.disableValue = true;
								}

								this.select1 = this.select1.replace('',this.currentStudent.recorrection[1].scoreObtained[0])
								this.select2 = this.select2.replace('',this.currentStudent.recorrection[1].scoreObtained[1])
								this.select3 = this.select3.replace('',this.currentStudent.recorrection[1].scoreObtained[2])
							}
						}
					}
				}
			}
		}
	}
}
</script>