<template>
	<div>
		<Login v-if="tokenAuxLog == null"></Login>
		<div v-if="plataformType == 0">
			<SearchStudent v-if="tokenAuxLog != null && showTableAux == false"></SearchStudent>
			<Evaluation v-if="tokenAuxLog != null && showTableAux == false"  ></Evaluation>
			<AddStudent v-if="tokenAuxLog != null && showTableAux == false"></AddStudent>
			<TableAux v-if="tokenAuxLog != null && showTableAux == true"></TableAux>
		</div>
		<div v-if="plataformType == 1">
			<Table v-if="tokenAuxLog != null"></Table>
			<Notifications v-if="tokenAuxLog != null"></Notifications>
		</div>
		<div v-if="plataformType == 2">
			<AdminTables v-if="tokenAuxLog != null"></AdminTables>
		</div>
	</div>
</template>


<script>
	//Vistas del usuario corrector
	import Evaluation from '../components/Evaluation.vue'
	import SearchStudent from '../components/SearchStudent.vue'
	import AddStudent from "../components/AddStudent"
	import Login from "../components/Login.vue"

	//Vistas del usuario administrador
	import AdminTables from '../components/admin/Tables'

	//Vistas del usuario supervisor
	import  Table from "../components/supervisor/Table.vue"
	import Notifications from "../components/supervisor/Notifications.vue"
	import TableAux from "../components/supervisor/TableAux"

	//Importaciones generales
	import {mapState} from 'vuex';

	export default {
	data: () => ({
		tokenAuxLog: null,
		plataformType: null,
	}),

	created:function () {
		this.tokenAuxLog = sessionStorage.getItem('acces_token')

		if (JSON.parse(sessionStorage.getItem('infoUser')) == null)
		{
			// eslint-disable-next-line no-console
			//console.log("hola");
		}

		else
		{
			this.plataformType = JSON.parse(sessionStorage.getItem('infoUser')).plataformRol
		}
	},
	components: {
		Evaluation,
		SearchStudent,
		AddStudent,
		Login,
		Table,
		TableAux,
		Notifications,
		AdminTables
	}, // eslint-disable-next-line no-mixed-spaces-and-tabs,no-undef
	computed: {
		...mapState(['logIn', 'showTableAux','serverRoute'])},
	}
</script>