<template>
<div>
			<v-card v-if="showNotifications" max-width="70%" style="margin-left:15%">
				<h4 style="text-align: center; color:white; background-color:#191970">Notificaciones activas</h4>
				<v-list three-line>
					<template v-for="(item, index) in items">
						<v-subheader
						v-if="item.header"
						:key="item.header"
						v-text="item.header"
						></v-subheader>

						<v-divider
						v-else-if="item.divider"
						:key="index"
						:inset="item.inset"
						></v-divider>

						<v-list-item
						v-else
						:key="item.title"
						>
							<v-list-item-avatar>
								<v-icon style="color:#191970 !important">email</v-icon>
							</v-list-item-avatar>

							<v-list-item-content>
								<v-list-item-title v-if="item.status == 0">{{item.title}} <v-chip label style="float:right; width:90px" class="" color="warning"> Sin acción</v-chip> </v-list-item-title>
								<v-list-item-title v-if="item.status == 1">{{item.title}} <v-chip label style="float:right; width:70px; text-aling:center" class="" color="green" text-color="white"> Leída</v-chip> </v-list-item-title>
								<v-list-item-subtitle class="mb-3">
									Generada el: {{item.date}}
								</v-list-item-subtitle>
								<v-list-item-subtitle >
									<v-btn @click="ignoreNotificationLocal(item)" :disabled="item.status != 0" text style="height:50%;  font-size:12px; color:#191970;" class="pl-0">Ignorar notificación</v-btn> 
									<v-btn @click.stop="showDialog(item)" text style=" height:50%; font-size:12px; color:red;">Eliminar producción</v-btn>

								</v-list-item-subtitle>
							</v-list-item-content>
						</v-list-item>
				</template>
				</v-list>

				<v-dialog v-if="currentItem != null" v-model="dialog" persistent max-width="400"> 
					<v-card>
						<v-card-title class="headline">¿Desea borrar la producción?</v-card-title>
						<v-card-text>Se eliminará la producción del evaluador con rut {{currentItem.rutCorrector}} del día {{currentItem.date}}</v-card-text>
						<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn color="#191970" text @click="dialog = false">Cancelar</v-btn>
						<v-btn color="red" @click="eraseProductionLocal(currentItem), dialog = false" text>Aceptar</v-btn>
						</v-card-actions>
					</v-card>
				</v-dialog>
			</v-card>
	
			<v-card class="mt-5" v-if="showNotifications" max-width="70%" style="margin-left:15%">
				<h4 style="text-align: center; color:white; background-color:#191970">Notificaciones de producciones eliminadas</h4>

				<v-list three-line>
					<template v-for="(item, index) in items2">
						<v-subheader
						v-if="item.header"
						:key="item.header"
						v-text="item.header"
						></v-subheader>

						<v-divider
						v-else-if="item.divider"
						:key="index"
						:inset="item.inset"
						></v-divider>

						<v-list-item
						v-else
						:key="item.title"
						>
							<v-list-item-avatar>
								<v-icon style="color:#191970 !important">email</v-icon>
							</v-list-item-avatar>

							<v-list-item-content>
								<v-list-item-title v-if="item.status == 2">{{item.title}} <v-chip label style="float:right" class="" color="red" text-color="white"> Eliminada</v-chip> </v-list-item-title>
								<v-list-item-subtitle >
									La producción de este profesor ha sido eliminada
								</v-list-item-subtitle>
							</v-list-item-content>
						</v-list-item>
				</template>
				</v-list>

				<v-dialog v-if="currentItem != null" v-model="dialog" persistent max-width="400"> 
					<v-card>
						<v-card-title class="headline">¿Desea borrar la producción?</v-card-title>
						<v-card-text>Se eliminará la producción del evaluador con rut {{currentItem.rutCorrector}} del día {{currentItem.date}}</v-card-text>
						<v-card-actions>
						<v-spacer></v-spacer>
						<v-btn color="#191970" text @click="dialog = false">Cancelar</v-btn>
						<v-btn color="red" @click="eraseProductionLocal(currentItem), dialog = false" text>Aceptar</v-btn>
						</v-card-actions>
					</v-card>
				</v-dialog>

			</v-card>
</div>
</template>

<script>
    import {mapState, mapMutations} from 'vuex';

	export default {
		data: () => ({
			items: null,
			dialog: false,
			closeNotifications: false,
			currentItem: null,
			items2: null
        }),
        beforeCreate()
		{
			this.$store.dispatch('getAllNotifications')
			this.$store.dispatch('getProductionDeleted')
		},
		methods: 
		{
			...mapMutations(['showNotificationAux','ignoreNotification','eraseProduction']),
			showDialog(item)
			{
				this.dialog = true;
				this.currentItem = item;

			},
			ignoreNotificationLocal(item)
			{
				this.ignoreNotification(item.id)
				this.currentItem = item
				this.closeNotifications = false
				this.$store.dispatch('getAllNotifications')
				this.$store.dispatch('getNonReadedNotifications')

			},
			eraseProductionLocal(item){
				this.eraseProduction(item)
				this.closeNotifications = false
				this.showNotificationAux();
				this.$store.dispatch('getProductionDeleted')
				this.$store.dispatch('getAllNotifications')
				this.$store.dispatch('getNonReadedNotifications')
				this.$store.dispatch('getAllStudents');
				this.showNotificationAux();
			},

		},
        computed: {
			...mapState(['allNotifications','showNotifications','productionDeleted'])
		},
        watch: 
		{
			allNotifications: function()
			{
				this.items = this.allNotifications
				/*this.items = [
					{ header: 'Today' },
					{
					avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
					title: 'Brunch this weekend?',
					subtitle: "<span class='text--primary'>Ali Connors</span> &mdash; I'll be in your neighborhood doing errands this weekend. Do you want to hang out?",
					},
					{ divider: true, inset: true },
					{
					avatar: 'https://cdn.vuetifyjs.com/images/lists/2.jpg',
					title: 'Summer BBQ <span class="grey--text text--lighten-1">4</span>',
					subtitle: "<span class='text--primary'>to Alex, Scott, Jennifer</span> &mdash; Wish I could come, but I'm out of town this weekend.",
					},
					{ divider: true, inset: true },
					{
					avatar: 'https://cdn.vuetifyjs.com/images/lists/3.jpg',
					title: 'Oui oui',
					subtitle: "<span class='text--primary'>Sandra Adams</span> &mdash; Do you have Paris recommendations? Have you ever been?",
					},
					{ divider: true, inset: true },
					{
					avatar: 'https://cdn.vuetifyjs.com/images/lists/4.jpg',
					title: 'Birthday gift',
					subtitle: "<span class='text--primary'>Trevor Hansen</span> &mdash; Have any ideas about what we should get Heidi for her birthday?",
					},
					{ divider: true, inset: true },
					{
					avatar: 'https://cdn.vuetifyjs.com/images/lists/5.jpg',
					title: 'Recipe to try',
					subtitle: "<span class='text--primary'>Britta Holt</span> &mdash; We should eat this: Grate, Squash, Corn, and tomatillo Tacos.",
					},
				]*/
			},
			productionDeleted: function()
			{
				this.items2 = this.productionDeleted;
			}
		},
		
    }
        
</script>